        <p class="title">Patch Notes</p>
        <p class="subtitle">1.4.2</p>
        <pre class="box">Platform Fighter Engine Full Release 1.4.2 - Patch Notes
(Last Modified: 10/16/2023)

Attacks
- Added Toon Link's Down Special.
	- This move spawns a bomb item that the user holds.
	- The bomb explodes after a certain amount of time, or if it is thrown.
	- If the bomb hits an opponent, the explosion won't damage the player who threw the bomb.
- Added Diddy Kong's Down Special.
	- This move throws a banana peel item behind the user.
	- If the banana peel hits an opponent in the air, it deals weak damage and knockback.
	- If the banana peel hits a grounded opponent, they instantly trip.
	- Other players can pick up and throw the banana.
- Added Pikachu's Up Special.
	- This move allows the user to quickly move in any direction.
	- You can optionally move a second time if you tilt the control stick in a different direction.
- General balance adjustments.

Fixes
- "stick_flick_cooldown" is now 2 frames by default, which should fix an issue with stick flicks being registered multiple times.
- "basic_item_throw" and "scalar_item_throw" no longer crash if the item is destroyed before the throw happens.
- The tumble state now uses "hitstun_grav" instead of the normal gravity value.

Extra
- The default max palette height is now 48 colors.
- The game now alerts you if you try to use a palette sprite that is larger than the max size.
- "pick_up_item" now has an argument for specifying the id of one item to be picked up.
- "hitbox_group_whitelist_id" can now be used on the "hitbox_group_array" of projectiles, if you set the "group_number" argument to "-1".
- Adjusted the volume of some sound effects.
- A small amount of folder reorganization.
- Fixed some comments.</pre>
        <p class="subtitle">1.4.1</p>
        <pre class="box">Platform Fighter Engine Full Release 1.4.1 - Patch Notes
(Last Modified: 8/10/2023)

Online Teams
- You can now play with Teams enabled in online games!
- The lobby host determines the teams of all the other players.
- Team Attack can be toggled from the private lobby menu.

Opera GX Mobile Export:
- The game now automatically enables Touch Controls if the screen height is greater than the width.
- Fixed an issue where multiple buttons could not be held at the same time on Touch Controls.
- Added a way to switch the Touch Stick Type on the Main Menu.
- Adjusted the Touch Stick sensitivity to make it easier to perform flicks.

Fixes
- Fixed an issue where "game_ggmr_event" would crash the game when a disconnect happens online.
- Fixed an issue where popups with zero choices would crash the game if the player pressed Up or Down.
- "obj_entity" has "image_speed" set to 0 by default now, so it won't animate while the game is paused.
- "obj_entity" now destroys all hurtboxes it owns immediately when it is destroyed, preventing a rare crash.
- Fixed a rare out-of-bounds crash with Favorite Colors on the Online CSS.
- The midair jump sprite no longer displays for 1 frame at the start of a grounded jump.
- Polygon's Turret now has a hurtbox with the correct scale.
- Fixed an issue where players picking Random could get a color chosen by a different player, causing the other player to swap colors.
- Fixed an issue where players could tell what other players online picked by seeing which colors were unable to be selected.
- Fixed an issue where many on-hit scripts would crash when hitting a destructible object such as Polygon's Turret.
- Fixed an issue where "stick_check" could return the wrong results when checking for the stick being tilted in a specific direction over multiple frames.
- All instance ids are now saved as "f64" instead of "s32" to prevent issues with comparisons on the Windows YYC export.
- "game_reset" now properly clears the replay data and win screen data for the match.
	- This means you can now save replays of match that were restarted.
	- This fixes a bug where the incorrect winner could be displayed if a match was restarted.
- "stick_check" now works correctly when checking if the stick was tilted on a single frame in the past.
- Fixed multiple comments that were incorrect.

Extra
- Added "PK Fire" as a premade attack.
- Added new hit restrictions: "not_grabbed", "not_launched", and "not_combo".
- "parry_ult_sprite" renamed to "parry_shield_sprite".
- "parry_ult_window" is now a macro named "parry_shield_window".
- "parry_ult_freeze_time" is now a macro named "parry_shield_freeze_time".
- "parry_ult_self_freeze_time" is now a macro named "parry_shield_self_freeze_time".
- Shield invulnerability is now set in fewer places.
- The midair jump and double jump sprites now have a slightly larger speed window for which they are used.
- Changed the default chat messages for online.
- Minor code formatting adjustments.
- PFE now uses a window with a border by default.
	- You can switch back to windowless mode in the GameMaker settings and by changing the "game_window_bar_enable" macro.
- The "custom screen size" in the options menu is now saved and loaded.
- The "Core" version of Platform Fighter Engine has been renamed to the "Free Trial" version.
- The official website has been moved from "springrollgames.github.io" to "platformfighterengine.github.io".</pre>
        <p class="subtitle">1.4.0</p>
        <pre class="box">Platform Fighter Engine Full Release 1.4.0 - Patch Notes
(Last Modified: 3/17/2023)

Stages
- Moved stage-specific properties out of "obj_stage_manager", into individual "stage init" scripts.
- Added the "stage_data" script which stores all data for stages, similarly to "character_data".
- "stage_data_get" now works similarly to "character_data_get", but for stages.
- Stage textures are now specified in "stage_data" instead of "obj_match_loading_screen".
- Added the function "stage_music_random_set", which picks a random stage music (with weighted chances).
- "stage_music_set" can now take an array for the arguments.
- "stage_choose_random" can now take an array of banned stages that cannot be chosen.
- Added a "foreground" array in "obj_stage_manager" that works the same as the "background" array, but draws to a higher layer.
- Added "obj_stage_animation_parent", which can be used to add custom animated objects to stages easily.
- Updated the graphics on all stages.
- Renamed some of the existing stages.
- Added a new stage "Biosphere".
- Updated the stage select screen to include the new stages.

Attacks
- General attack balance changes.
- Renamed all attacks to use the format "character + move slot + special name".

Items
- Added a new "Shotgun" item.
- "override_item_attacks" now returns the correct values.
- Items with colors that are on a character's palette will no longer recolor when you attack with them.

Fixes
- "palette_shader_set" and "palette_shader_rgb_set" now have the correct default for the "fade" argument.
- Fixed an issue with "key_to_string" returning the incorrect character for some keyboard keys.
- "basic_dspec_earth_wall" no longer reflects your own projectiles.
- "game_state_print_id_debug" works again.

Extra
- Major code formatting changes.
- Major file organization changes.
- Major naming convention changes.
- Deleted unused files.
- The win screen now shows additional data from the match.
- You can rename a replay before saving it on the win screen, and rename a replay from the Replay menu.
- Added support for generic pop-up messages on menus.
- Added a confirmation pop-up for deleting replays.
- Added utility functions for drawing sprites or textures spread evenly between 4 corners ("draw_sprite_spread" and "draw_texture_spread").
- Changed the names of "CHARACTER_DATA" properties, and added a new property for the sprite shown when a character is selected.
- Added the function "player_count" to get the number of players stored in the player data more easily.
- Buttons on the main menu now have an outline when hovered.
- The game timer has a new look for "HUD_TYPE.normal".
- The intro and outro text is now drawn with a font.</pre>
        <p class="subtitle">1.3.4</p>
        <pre class="box">Platform Fighter Engine Full Release 1.3.4 - Patch Notes
(Last Modified: 1/15/2023)

Attacks
- General balance changes.
- "fspec_eiha" now deals damage over time.
- Added a new attack, "dspec_earth_wall".

Stages
- A new template stage has been added: "Smiley Floats"!

Functionality
- Added grab clank struggles when players grab each other at the same time.
- Added an example script "poison_passive" for damage-over-time status effects.
- You can now taunt on the respawn platform.
- Added a scoreboard between stocks for 1v1 matches.

Fixes
- Added a cooldown for footstools to prevent infinites.
- Footstooling no longer uses up one double jump.
- "rm_practice_room" now has a white background color by default, so you can see the stage.
- "live_value" now reads values as real numbers automatically (unless specified not to).
- Fixed a bug where "fspec_shadow_flare" would crash if the target instance was destroyed before the flares hit.
- Fixed "ex_meter_overhead" not positioning the bars correctly.
- "apply_damage" now correctly creates damage numbers over the player being damaged, instead of the player calling the script.
- Fixed an issue where the macros related to balloon knockback did not work as intended.
- Launch trajectories are now more accurate.
- Fixed some issues with the touch controls stick.
- Fixed some collision issues with moving platforms and the Explosive Box item.
- Tap Jump no longer buffers multiple jump inputs.
- Perfect shielding now works again.

Extra
- Decimal point damage is now drawn smaller on the HUD.
- "calculate_smash_damage" no longer rounds the amount.
- Added sound effects for shield start and shield release.
- Players standing on the ground will now be KO'd by the top blastzone. This means that platforms moving upwards can KO players now.
- Players can no longer be Screen or Star KO'd on their last stock, or in the last 10 seconds of a match.
- Changed a few hitbox interactions with the Smash Ball item to be more intuitive.
- Players with the "Smash Stick" setting can no longer perform smash attacks in certain states that don't normally allow smash attacks.
- Updated the docs for "game_surface_enable" to mention that disabling the game surface also disables Clip Saving.
- "obj_stage_manager" now has a "custom_ids_struct" variable you can use.
- PFE now officially uses the Long-Term Stable (LTS) branch of GameMaker.
- Removed blank folders from the project, and fixed filename capitalization inconsistencies.
- Updated comments to be more consistent for the documentation.
- Updated copyrights to 2023.</pre>
        <p class="subtitle">1.3.3</p>
        <pre class="box">Platform Fighter Engine Full Release 1.3.3 - Patch Notes
(Last Modified: 9/14/2022)

PFE: Core
- Introducing a new version, "Platform Fighter Engine: Core"
- This is a FREE version of PFE available for download on itch.io, replacing the Beta 0.7.1 version.
- It has almost all of the features from the Full Version, except rollback functionality.

Attacks
- General balance changes.
- Added Monado Arts.

Functionality
- Added multiplier variables for player attack damage, attack knockback, and damage taken.
- Added support for footstools.
- You can now change the background color of menus from the room editor.

Fixes
- Fixed an issue where the game would crash if all remaining players were KO'd on the same frame.
- You can no longer hit or grab the shields of invincible players.
- Fixed an issue where the game would crash if you took control of a replay without having a default online profile.
- The "rewind" prompt is no longer shown in Replay Camera Mode if replay rewinding is disabled.
- "getup_active" and "getup_endlag" are now properly saved and loaded during rollbacks.
- Fixed an issue where pressing Escape to reset the window size wouldn't always be possible.

Extra
- Added a script "calculate_jump_height", so you can more easily compare the jump heights of characters.
- The CPU now properly chooses a random getup option when knocked down.</pre>
        <p class="subtitle">1.3.2</p>
        <pre class="box">Platform Fighter Engine Full Release 1.3.2 - Patch Notes
(Last Modified: 9/1/2022)

Attacks
- General balance changes.
- Added Cross Slash (non-limit version).
- Added Climhazzard.
- Added Witch Time.
- Added a passive script to allow using Shoryuken through the motion input.

Functionality
- Added support for non-integer damage amounts.
- Added an example passive script for Auto Turnaround.
- Added support for chip damage through shields.
- Added support for attacks that trip opponents.
- "find_nearest_player" now has an optional argument for players to ignore.

Fixes
- Fixed an issue where a player's body hurtbox would have less powershield frames than the shield hurtbox if shield poking was turned on.
- The "GO!" is now drawn correctly at the start of each match.
- Fixed an issue where "hurtbox_anim_match" would not round the player's "anim_frame" correctly.
- Fixed an issue where local frame skip activating on the last frame of a match would cause a crash.
- "move_grounded" is now more precise with how close it allows players to get to the edge of blocks.

Extra
- Reworked parts of the Character Init Scripts to be more concise.
- Pressing F8 to turn off visible launch trajectories now clears the screen of any drawn launch trajectories.
- You can now press Escape at any time to exit fullscreen and reset the window size.
- Added new sound effects for Star KO and Screen KO.
- The respawn platform is now automatically scaled to match the width of character sprites.
- Touch controls can now be enabled by clicking a button on the main menu.
- Renamed various scripts to fit the overall naming conventions.</pre>
        <p class="subtitle">1.3.1</p>
        <pre class="box">Platform Fighter Engine Full Release 1.3.1 - Patch Notes
(Last Modified: 7/8/2022)

Attacks
- General balance changes.

Fixes
- Fixed an issue where players wouldn't reset their animation when being knocked out, which could crash the game.
- Fixed camera-related issues in "final_smash3".
- Fixed an issue where players wouldn't be knocked out correctly when using "final_smash3" offstage.
- Fixed an issue with "grab_break" being able to revive knocked out players.
- Certain overhead drawing scripts no longer draw during the final hit effect.

Extra
- Collision Compatibility Mode is now turned off, and all places in the code that used bbox_right or bbox_bottom have been adjusted.
- Autogenerated profiles are now removed more frequently.</pre>
        <p class="subtitle">1.3.0</p>
        <pre class="box">Platform Fighter Engine Full Release 1.3.0 - Patch Notes
(Last Modified: 7/5/2022)

Final Smashes
- Added a Final Smash for each of the 5 premade characters.
- Added a Smash Ball item.
- Added a match setting for the Final Smash meter.
- You can now turn off automatic camera movement if you want to manually control the camera.
- Added the option to fade the background of a stage with any color.
- Added a variable "ignore_blastzones" that allows players to ignore blastzones.
- You can now set a player's hurtbox sprite to -1 to effectively remove their hurtbox.
- Added the "cutscene" game state that turns off the HUD and only runs certain object events.
- You can now make players invisible.

Attacks
- General balance changes.
- Fixed an issue where "fspec_falcon" wouldn't have more lag when hitting shields.
- Added Byleth's Up B.
- Added a new Forward Smash for Radian.

Functionality
- Added basic support for touch controls.
- Added a match setting for an EX meter, and added EX forms of some special moves.
- Added a match setting for Screen Wrap Stamina mode.
- Added support for grab breaking (also called grab teching).
- If two players grab each other on the exact same frame, a grab break occurs instead of both players going into grab release.
- Added ledge mantling.
- Added the option to scale KO time by how far away the attacker is from the center of the stage.
- Added support for making the player flash colors.
- Added the option to prevent players from grabbing opponents behind them.
- Added functions to copy properties between hitboxes and hurtboxes.
- Added a property "detect_multihit" for detectboxes that allows them to detect the same opponent multiple times.
- Added a function for drawing speed lines moving in a direction.
- Added callbacks for players landing hits, and getting hit by other players.

Fixes
- Fixed an issue where grab release knockback make the player reeling if the last attack that hit them made them reeling.
- The CPU "Shield Attack" type now properly attacks.
- Slope bounce angles have been adjusted to prevent issues with certain knockback angles on slopes.
	- You can still use the old bounce code by setting the "slope_type" of individual slope instances.
- Players now properly bounce off the straight edges of slopes.
- Shield dropping is now performed by releasing shield and then pressing down. This means keyboard players can now tilt shield downward on plats.
- Picking up a weapon-type item with a Jab on the simplified item system will no longer start the item attack instead of the Jab.
- The game no longer crashes if you try to use a negative number for the negative input delay.
- Fixed an issue where you could add more MIS devices than the specified limit sometimes.
- Players hit at 90 degrees or 270 degrees will no longer sometimes turn around.
- Fixed an issue where "anim_loop_continue" would not properly set all of the animation variables.
- Fixed an issue where players could get stuck in the ledge tether animation without grabbing the ledge.
- The player's collision box now turns around to face the player's direction (this only affects asymmetrical collision boxes).
- Fixed an issue where hitting an opponent on the last lifetime frame of multiple hitboxes could result in some of the hitboxes being destroyed before hitlag ended.
- Players can no longer be in the Hitlag state and have self hitlag frames.
- Projectiles now experience hitlag.
- Fixed an issue where many attacks with detectboxes would not check the correct opposing hurtbox.
- Clip saving should now work when camera zoom is enabled.
- Fixed an issue where players could input underscores into some text fields, causing the game to crash.
- Fixed an issue where Share Stock could be used even if Teams were turned off.
- Fixed issues with win conditions for certain rulesets.
- The camera should zoom in and out more smoothly during rollbacks now.
- Damage colors in stamina matches are no longer in reverse.
- The "player_hud_scale" macro now works as intended.
- Fixed an issue where players could occasionally drop through platforms to cancel landing lag.

Extra
- Added a simple in-game command line that be used to set variables during gameplay for debugging purposes.
- Added an in-game option for "Hold to Pause".
- Ledge tethers now have a black outline.
- The hitlag animation is now delayed for 1 frame.
- You can now set a drift DI lockout time.
- The damage of players is now displayed during the KO animation.
- Added another type of HUD that can be used.
- The win screen now shows what teams players were on.
- You can now delete replays from the in-game menu.
- Favorite color palettes are now stored for each profile.
- The CSS has more readable colors.
- Replaced most DS maps and lists in "obj_player" with structs and arrays.
- Removed unused scripts.
- Removed un-optimized rollback saving and loading.
- The game now crashes with a specific message if you turn on items and go to a stage with no item spawners.
- Changed the argument for "allow_hitfall" to make more sense.
- The "Smash Stick" control option is now OFF by default, making it easier to use tilts.
- Renamed various macros to be more consistent.
- Removed all uppercase letters from assets, and renamed various assets to be more consistent.
- Added in-game credits that are automatically generated from the "credits.txt" file.</pre>
        <p class="subtitle">1.2.1</p>
        <pre class="box">Platform Fighter Engine Full Release 1.2.1 - Patch Notes
(Last Modified: 3/23/2022)

Attacks
- Added the "simple attacks" system, which allows you to define attacks dynamically through data instead of through scripts
- Simple attacks cannot be customized as much as normal attack scripts, but are quicker to make
- Added the following moves as premade simple attacks:
	- fair_shulk
	- fsmash_roy
	- nspec_hadoken
	- grab0
	- pummel0
	- bthrow0
	- fspec_wind
	- utilt3
	- bair_cloud
- General balance changes

Stages
- Added a new stage template, "Flipped Battlefield"

Functionality
- Added support for the knockdown state
- Players in the knockdown state can normal getup, roll, or getup attack
- Certain moves can lock players in knockdown, forcing them to use a normal getup
- Low knockback moves no longer put the opponent in the tumble state
- High knockback moves now use a "reeling" animation
- Added support for "singleplayer mode", which doesn't end the game when only one player is left
- Added share stock
- "collision_box_change" now has an optional argument for an anchor point to use

Fixes
- Players can no longer use negative input delay online
- Fix some dependency issues with the GGMR functions
- "fspec_falcon" no longer crashes when hitting an explosive box
- The game no longer crashes at the end of a match with debug sync testing turned on
- Fixed an issue where many attacks with custom hurtboxes would not set the hurtbox on the first frame
- "obj_item_ball" can no longer get stuck in an infinite collision
- Armoring through moves now gives players the full amount of hitlag
- Ledge rolling now uses the tech roll state instead of the normal roll state, so you can no longer wavedash cancel a ledge roll
- Fixed some VFX and camera shake inconsistencies with missed techs
- Fixed an issue where players would sometimes animate even if "self_hitlag_frame" was true
- Fixed an issue where a rock colliding with 2+ players at once could cause desyncs online
- Fixed an issue where chat messages would not order correctly if sent before and after midnight

Extra
- Simplified the buffer functions for GGMR saving and loading (this may cause issues if you try to merge 1.2.1 with 1.2.0)</pre>
      <p class="subtitle">1.2.0</p>
      <pre class="box">Platform Fighter Engine Full Release 1.2.0 - Patch Notes
(Last Modified: 2/25/2022)

Items
- Added basic support for items, with a few example items:
	- Explosive Box: A box that acts as a platform, and explodes on hit
	- Flip Field: A field that heals or damages players, and changes every time it is hit
	- Rock: A solid rock that can be picked up and thrown around
	- Ball: A ball that can be thrown around and bounces off players
	- Bat: A slow but extremely powerful item
- You can adjust item spawn rates in "Game_Settings" and "item_spawn_script"
- Item spawn zones have been added to every stage

Characters & Stages
- Added the 5th character, Radian
- It has a brand-new moveset, and shares animations with the other small characters
- The Clouds stage has a new layout and new sprites

Attacks
- General attack balance changes
- Fixed the hurtboxes on some attacks
- Added Power Geyser
- Added Clairen's Fair
- Added Radian's entire moveset

Functionality
- Added the option for clip saving, which stores the last few seconds of gameplay during local matches and allows you to save GIFs from the pause menu
- The replay menu now displays metadata from the selected replay, including total length, match settings, stage, and players
- You can fast forward and rewind when watching replays
- You can change the match settings on the Private Lobby screen
- You can assign character-specific CPU input scripts
- Added a toggle for offscreen radar from Ultimate
- Added settings for the scale of sprite on the CSS, HUD, and win screen
- Added a new CPU type "Shield Hold" that holds shield as long as you are attacking
- You can set custom scripts for hit VFX creation
- Added a new hit restriction "ledge_only" that makes hitboxes only hit players hanging on the ledge
- Added the option to end players' respawn invincibility after attacking once
- Added the option to prevent players from airdodging upwards out of jumpsquat
- Added moonwalking, which can be enabled in "Game_Settings"
- "switch_character" no longer forces the character to change back to the Idle state
- Added a new callback system that allows you to assign multiple scripts to a variable to be called by the engine later
	- The passive, HUD, and overhead scripts now use the callback system
	- The "attack_draw_script" has been replaced with "draw_begin_callback" and "draw_end_callback", which can also be used outside of attacks
- Added the in-game option to use stereo sound effects

Fixes
- Fixed an issue with "move_bouncing" where the player could get stuck on surfaces
- Fixed multiple interactions with moving blocks and slopes
- "switch_character" now destroys the player's previous hurtbox, so it doesn't create infinite hurtboxes
- Palette shaders now work on HTML5 / Opera GX exports
- Fixed a LOT of HTML5 specific crashes and bugs
- The game no longer infinitely loops if there are more players than possible colors in the palette
- Fixed an issue with fastfalling when the fastfall speed was set to a non-integer value
- Fixed a few issues with Polygon's floaty jump
- The default online profile is now stored as a name instead of an index, meaning if you delete a different profile it will no longer change
- Prompts in menus now change if you modify the macros for menu buttons and keys
- The camera is now restricted by the blastzones, instead of room size
- Fixed an issue where "obj_vfx" would update the sprite 1 frame faster than intended. All "vfx_create" in the base engine have been changed to have a 1 frame longer lifetime
- The KO sound effect no longer plays at the start of a Screen KO or Start KO
- Fixed a few issues with dropping through platforms while running
- Fixed an issue where connecting a new controller on the CSS would not re-activate the cursors of players currently in menus
- Fixed an issue where the Haven stage would not display the correct sprites after the transformation
- Fixed an issue where a tolerance value of 0.0 would not work in palette shaders

Extra
- PFE uses a borderless window now, and has a top bar that can be used to drag around the window or change the scaling
- Objects that use the outline shader now draw onto a surface first, so texture groups can be cropped
- Stage data values for the CPU to use are now set in "obj_stage_manager" instead of "CPU_Input"
- On the CSS, character zones and player windows now have clipping masks when drawing the selected character
- If you do not have any default online profile set, it will automatically be set to the first profile you make
- Replaced "obj_voronoi" with "obj_menu_background", which draws a different pattern that is compatible with all platforms
- The menu sidebar now has an option to go back to the Main Menu
- The cursor speed is now consistent across all menus
- You can now use the Dpad to move the cursor on menus
- Swapped which buttons are for Confirm and Back on menus to match the Xbox layout instead of the Nintendo layout
- On the local CSS, cursors can now wrap around the screen
- You can now hold the back button on the CSS to open up the menu sidebar
- The character select now changes your character as you move the token over character zones
- The character select and stage select screens have new animations
- You can now pause during the starting countdown
- Players in shieldstun now have a shield with static on it like in Ultimate
- Added a setting to show the shieldstun amount when a hitbox is blocked
- Added a setting to show the number of advantage frames of each hit
- Renamed "shield_stun" to "shieldstun" for consistency
- Renamed "fast_fall_speed" to "fastfall_speed" for consistency
- All of the premade grab attacks can no longer hit opponents on the ledge
- Optimized saving and loading can now be toggled with a macro
- Added "game_state_optimizer.js" to the files, which can be used to generate the "__optimized" scripts for rollback
- The PFE website now has a tutorial for setting up the included matchmaking server file
- Added support for negative input delay in local matches (use at your own risk!)</pre>
      <p class="subtitle">1.1.0</p>
      <pre class="box">Platform Fighter Engine Full Release 1.1.0 - Patch Notes
(Last Modified: 11/24/2021)

Netcode
- Added the ability to spectate private lobby matches
- You can now have up to 8 member in an online lobby, with up to 4 players and the rest as spectators
- Further optimized game state saving & loading
- Added a simple chat feature with preset messages

Stages
- Added new music tracks
- Haven now has a special transformation when all players reach 1 stock, or when there is 1 minute left
- You can now set shaders that will apply to the entire screen similarly to the built-in day/night shader

Attacks
- General attack balance changes
- Added Sephiroth's Bair
- Added Sephiroth's Side B
- Added Chrom's Fsmash
- Added Bowser's Fair
- Added a new Dash Attack

Functionality
- Added support for detecting motion inputs
- Ledge tethers now use realistic physics
- You can now manually set the blastzone coordinates
- Added heavy armor
- Crouching now has weak heavy armor
- Added a setting to limit the number of times players can grab the ledge before touching the ground again
- Added a setting to prevent players from getting ledge invincibility after grabbing the ledge a certain number of times
- Added a setting to view hitbox trails
- You can now assign an array of sprites or animation arrays to the my_sprites map, and the game will randomly choose one every time that animation is set
- Added a new hit restriction, "combo only", where hitboxes can only hit opponents in hitstun
- Added "Local Frame Skip", so if the game is running under 60fps in local matches, it will try to run additional frames without drawing to catch back up
- Added a function "character_choose_random", which is used in every spot the game needs to pick a random character, and allows you to ban specific characters
- The palette system has been overhauled, and you can now dynamically change colors on a palette during gameplay
- Added the functions "entity_owner_change" and "hitbox_owner_change"
- Added the option to turn off the Shield + Attack input for grabbing
- Removed the option for setting the default input delay online. The default input delay is now determined by the ping when the online CSS starts
- You can now assign methods to UI instances, instead of being restricted to scripts

Fixes
- Fixed an issue where the input system was saving more data into the buffer than necessary
- Fixed an issue where players could walk while facing the wrong direction
- Fixed an issue where players could start smash attacks right after a ledge jump
- Fixed an issue where holding downwards on the frame that a player collided with a platform would cause the player to float on top of the platform without landing
- Fixed an issue where players could dash twice with a single flick if the dash length was low enough
- Fixed an issue where assigning an array of sprites to the my_sprites map would crash the game
- Fixed an issue where KOing players with C4 in a timed match with infinite stock would not give you any points
- Fixed an issue with camera shake rounding in "hit_vfx_style_create"
- Fixed an issue where players launched exactly into a corner would not collide with the corner
- Fixed a crash in online menus if the game received a packet with an unrecognized type
- Renamed all "fx" assets to "vfx", to avoid name collisions with the new functions in GMS2.3.6
- The left and right Ctrl keys now show up on the custom controls screen
- The "character_static_" macros now work properly when set individually
- Fixed a rare crash from "fspec_cloudburst"
- Grabbing now resets the target's invulnerability
- Shieldstun no longer stacks
- Players now always face away from the wall after wall teching
- "move_x_grounded" now checks the edge buffer properly once again
- Palettes with transparent colors now use the proper alpha in-game

Extra
- Added more subimages for slopes to use, which will increase collision accuracy
- Made it easier to perform a Reverse B
- Neutral Airdodge can no longer waveland snap
- The F4 debug overlay now shows a trail of the control stick's previous positions
- Renamed the "Techrolling" state to "Tech Rolling" so it fits the naming conventions
- Starting attacks no longer deletes the corresponding input from the input buffer
- The "length" entry in enums is now "LENGTH" so it won't be as confusing
- Setting the stock or time to 0 now displays an infinity sign
- In Timed matches with infinite Stock, each player's score is now displayed on the HUD
- The camera now shakes when players are hit into blocks and miss the tech
- Added a few statistics to the startup screen, such as total playtime and online wins
- The CPU can now wavedash
- The logo and startup animation have been slightly changed</pre>
