<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Setting up the Matchmaking Server
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Overview</p>
      <p>
        PFE comes with a basic <strong>Node.js</strong> matchmaking server included, called <code>server_public.js</code>.
        You can find it in the "Included Files" section of GameMaker, or by looking in the "datafiles" folder from the file explorer.
      </p>
      <br/>
      <p>
        In order to properly use the matchmaking server, you will need to find a computer that can run the file and has a static public IP address.
        This tutorial will demonstrate how to set up the server on <a href="https://aws.amazon.com/lightsail/" target="_blank" rel="noopener">Amazon Lightsail</a>,
        but if you already have another way to run <strong>Node.js</strong> code with a static public IP address, you can use that instead.
      </p>
      <br/>
      <p>
        Additionally, this tutorial (and the <code>server_public.js</code> file) are only set up to have a single instance of the server running.
        If you need a more dynamic matchmaking system, you'll have to write your own server code.
      </p>
      <br/>
      <p class="title">Amazon Lightsail Setup</p>
      <p>
        Once you sign up for Amazon Lightsail, select the option to create a new instance.
        Choose the region you want, and make a <strong>Linux / Node.js</strong> server.
        The matchmaking server doesn't use a lot of resources, so you can select the cheapest tier.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta1_1.png" class="image"/>
      <br/>
      <img src="@/assets/Tutorials/ta1_2.png" class="image"/>
      <br/>
      <p>
        Once the instance is created, you need to give it a static IP address.
        This is a necessary step - if you don't assign a static IP address, then the server will change IP every time you start and stop it, meaning the game will no longer be able to contact it.
        Read the official instructions <a href="https://lightsail.aws.amazon.com/ls/docs/en_us/articles/lightsail-create-static-ip" target="_blank" rel="noopener">here</a>.
      </p>
      <br/>
      <p>
        You'll also need to set up the instance's Firewall, so it can receive UDP packets on the correct port. It should look something like this (depending on which port you choose to use):
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta1_7.png" class="image"/>
      <br/>
      <p class="title">The Server File</p>
      <p>
        Before transferring the <code>server_public.js</code> file to the lightsail instance, you need to make sure it is compatible with your game.
        Open up the <router-link to="/docs/engine_settings">engine_settings</router-link> file, and make sure that the following requirements are met:
      </p>
      <br/>
      <ol>
        <li>
          The server's <code>supported_game_versions</code> array must include the game's
          <router-link to="/docs/version_string">version_string</router-link>.
        </li>
        <li>
          The server's <code>latest_version</code> is equal to the <router-link to="/docs/version_string">version_string</router-link>.
        </li>
        <li>
          The server's <code>server_keys</code> object must have keys that match the
          <router-link to="/docs/server_key_private_lobby">server_key_private_lobby</router-link> and
          <router-link to="/docs/server_key_random_matchmaking">server_key_random_matchmaking</router-link> strings.
        </li>
        <li>
          The server's <code>udp_port</code> variable matches the <router-link to="/docs/server_port">server_port</router-link> macro.
        </li>
        <li>
          The <router-link to="/docs/server_ip_address">server_ip_address</router-link> macro matches the static public IP of your lightsail instance.
        </li>
      </ol>
      <br/>
      <img src="@/assets/Tutorials/ta1_3.png" class="image"/>
      <br/>
      <p>
        If any of these are NOT set correctly, the game may try to connect to the wrong server, or the server may ignore the game's packets.
      </p>
      <br/>
      <p>
        Next, transfer the <code>server_public.js</code> file to the lightsail instance.
        Read the official instructions for setting up <strong>FileZilla </strong>
        <a href="https://lightsail.aws.amazon.com/ls/docs/en_us/articles/amazon-lightsail-connecting-to-linux-unix-instance-using-sftp" target="_blank" rel="noopener">here</a>.
        Make sure to write down the file path, so you can find the file later to run it.
      </p>
      <br/>
      <p class="title">Running the Server</p>
      <p>
        Once the file is transferred, return to the lightsail dashboard, and select your instance.
        Click on the "Connect using SSH" button.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta1_4.png" class="image"/>
      <br/>
      <p>
        Use the <code>cd</code> command to navigate to the folder you put the file in. You can check if the file's there with the <code>ls</code> command.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta1_5.png" class="image"/>
      <br/>
      <p>
        To run the server, use the command <code>nohup node server_public.js &</code>. 
        This will start the <strong>Node.js</strong> code, and allow it to keep running even after you disconnect from the instance.
      </p>
      <br/>
      <p>
        Run your game to make sure it can connect to both the Random Matchmaking and Private Lobby services.
        For Random Matchmaking, it should display "Waiting for a match...".
        For Private Lobbies, you should be able to change the lobby code and send join requests.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta1_6.png" class="image"/>
      <br/>
      <p>
        And that's it! Use the <code>exit</code> command in the SSH terminal to disconnect, and the server should keep running in the background.
      </p>
      <br/>
      <p class="title">Stopping the Server</p>
      <p>
        In order to stop the matchmaking server, connect to your instance through SSH.
        Use the <code>ps aux</code> command to bring up a list of all processes running on the instance.
        Find the <code>node server_public.js</code> process, and locate the <strong>PID</strong> number.
        Use the <code>kill</code> command with the <strong>PID</strong> to stop the process.
      </p>
      <br/>
      <p class="title">Extra</p>
      <p>
        That's all it takes to run the default matchmaking server, but there are a few extra things you might want to know.
      </p>
      <br/>
      <p>
        <strong>Debug Output</strong>:
        By default, all debug output is written to the <code>nohup.out</code> file.
        If you want to see the output in the terminal, use the command <code>node server_public.js &</code> to run the file.
        <i>This will cause the program to close when you disconnect however, so it should only be used when debugging.</i>
      </p>
      <br/>
      <p>
        <strong>Log Files</strong>:
        The matchmaking server periodically writes log files, which will be created in the <code>logs</code> directory.
        You can transfer these files back to your computer to view.
        All of the log files except the <code>stats_log.json</code> can be deleted from the server every once in a while to save space.
      </p>
      <br/>
      <p>
        <strong>Config File</strong>:
        You can optionally transfer a <code>config.json</code> file to the server, which will be read when the server file is run.
        This can hold various settings, and means you only need to replace the config file and re-run the server to change settings,
        instead of having to replace the entire server file.
        Check the <code>server_public.js</code> code to see which settings can be added to the config file.
      </p>
      <br/>
      <p>
        <strong>Sending Data to the Server</strong>:
        The function <router-link to="/docs/server_send_packet">server_send_packet</router-link> is when sending any packets to the server.
        You can modify it if you want to change the protocol.
      </p>
      <br/>
      <p class="title">Summary</p>
      <p>
        - The <code>server_public.js</code> file needs to be run on a computer with a static public IP address. Amazon Lightsail is one service that provides this
      </p>
      <p>
        - The values in <router-link to="/docs/engine_settings">engine_settings</router-link> must match those in your server file, otherwise the game will not be able to connect
      </p>
      <p>
        - You can use 
        <a href="https://lightsail.aws.amazon.com/ls/docs/en_us/articles/amazon-lightsail-connecting-to-linux-unix-instance-using-sftp" target="_blank" rel="noopener">FileZilla </a>
        to transfer files back and forth from the server
      </p>
      <p>
        - Use the command <code>nohup node server_public.js &</code> to start the server. Use <code>ps aux</code> and <code>kill</code> to stop the server
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>