<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to add a New Player State
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">State Setup</p>
      <p>
        All player states must have an entry in the <code>PLAYER_STATE</code> enum, which can be found in the <router-link to="/docs/enums">enums</router-link> script.
        For this tutorial, we'll add a new state called <strong>"airdash"</strong> to the enum.
        The order of states in the <code>PLAYER_STATE</code> enum does not matter, so you can put it wherever you'd like.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_1.png" class="image"/>
      <br/>
      <p>
        Next, we need to officially name the state in the <router-link to="/docs/player_state_name_get">player_state_name_get</router-link> script.
        Add a new case to the <code>switch</code> statement like this:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_2.png" class="image"/>
      <br/>
      <p>
        The name doesn't need to match the enum entry. This can be seen for states like <code>PLAYER_STATE.rolling</code>, where the name is simply "Roll".
      </p>
      <br/>
      <p class="title">The State Script</p>
      <p>
        The next step is to add a state script for our new airdash state.
        Following the naming conventions, it should be called <code>standard_airdash</code>.
        You can duplicate one of the existing state scripts so you have a base to work with:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_3.png" class="image"/>
      <br/>
      <p>
        All state scripts should use this <code>switch</code> statement.
        The <code>PLAYER_STATE_PHASE.start</code> case only runs on the frame the state is entered, and is generally used to set the animation for the state.
        The <code>PLAYER_STATE_PHASE.normal</code> case contains all of the code that the state will run every frame.
        The <code>PLAYER_STATE_PHASE.stop</code> case runs code when the state ends, before the player's new state runs its Start phase.
      </p>
      <br/>
      <p>
        For the airdash state, we'll add some code in the Start phase to have it change the player's animation, and set the player's speed in a specific direction:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_4.png" class="image"/>
      <br/>
      <p>
        We can use the <code>state_frame</code> variable in the same way we use the <code>attack_frame</code> variable in attack scripts.
        Since an airdash should only last a few frames, we'll set it to a low number:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_5.png" class="image"/>
      <br/>
      <p>
        Now for the Normal phase of the state.
        Airdashes should be cancelable with attacks, so we'll add scripts to allow the player to attack.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_6.png" class="image"/>
      <br/>
      <p>
        If we want to let players waveland from an airdash, that's just another line of code:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_7.png" class="image"/>
      <br/>
      <p>
        We also need to make sure the state ends when the timer runs out:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_8.png" class="image"/>
      <br/>
      <p>
        Lastly, we need to call a movement script, so the player moves during this state.
        Even if we use <router-link to="/docs/speed_set">speed_set</router-link>, the player won't move anywhere without a specific movement script being called.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_9.png" class="image"/>
      <br/>
      <p>
        The airdash state doesn't really need anything to happen when the state is stopped, so we can leave the <code>PLAYER_STATE_PHASE.stop</code> case empty.
      </p>
      <br/>
      <p class="title">Adding the Script to Characters</p>
      <p>
        After the script has been finished, we need to add it to <router-link to="/docs/player_states_init">player_states_init</router-link>.
        This means all characters in the game will use our new script when they are in the airdash state.
        If you wanted one character to have a different airdash state than every other character, you would have to give them a different script in their character init script.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_10.png" class="image"/>
      <br/>
      <p>
        To test this state, we need a way for players to trigger an airdash.
        For this tutorial, we'll simply change the code in <router-link to="/docs/check_airdodge">check_airdodge</router-link> to set the player to the airdash state instead of the airdodge state:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_11.png" class="image"/>
      <br/>
      <p>
        Run the game now and try airdashing around!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb7_12.gif" class="image"/>
      <br/>
      <p class="title">The Full Code</p>
      <pre>///@category Player Standard States
/*
This script contains the default Airdash state characters are given.
*/
function standard_airdash()
	{
	//Contains the standard actions for the airdash state.
	var run = true;
	var _phase = argument_count > 0 ? argument[0] : PLAYER_STATE_PHASE.normal;
	switch(_phase)
		{
		case PLAYER_STATE_PHASE.start:
			{
			//Animation
			anim_set(my_sprites[$ "Airdodge"]);
			
			//Speed
			var _speed = 10;
			if (stick_tilted(Lstick))
				{
				//Move in the direction of the left stick
				var _dir = stick_get_direction(Lstick);
				speed_set(lengthdir_x(_speed, _dir), lengthdir_y(_speed, _dir), false, false);
				}
			else
				{
				//Defaults to going forward
				speed_set(_speed * facing, 0, false, false);
				}
				
			//Timer
			state_frame = 12;
			break;
			}
		case PLAYER_STATE_PHASE.normal:
			{
			//Attacking
			if (run && allow_smash_attacks()) then run = false;
			if (run && allow_special_attacks()) then run = false;
			if (run && allow_aerial_attacks()) then run = false;
			
			//Waveland cancel
			if (run && check_waveland()) then run = false;
			
			//Return to the aerial state
			if (run && state_frame == 0)
				{
				state_set(PLAYER_STATE.aerial);
				run = false;
				}
				
			//Move, without going through platforms
			move_hit_platforms();
			break;
			}
		case PLAYER_STATE_PHASE.stop:
			{
			break;
			}
		}
	}
/* Copyright 2024 Springroll Games / Yosi */</pre>
      <br/>
      <p class="title">Summary</p>
      <p>
        - All states need an entry in the <code>PLAYER_STATE</code> enum, as well as the <router-link to="/docs/player_state_name_get">player_state_name_get</router-link> script
      </p>
      <p>
        - All states need a "standard" state script
      </p>
      <p>
        - Adding the script to <router-link to="/docs/player_states_init">player_states_init</router-link> sets it as the default for all characters, but you can override it in the character init scripts
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>