<template>
	<div class="container">
		<section class="hero is-primary">
			<div class="hero-body">
				<p class="title">
					How to add a New Menu Screen
				</p>
				<div>Last updated for Version 1.4.1</div>
			</div>
		</section>
		<section class="section main-part">
			<p class="title">Menu Rooms</p>
			<p>
				Every menu screen uses a separate Room resource.
				For this tutorial, create a new Room named <code>rm_trophies</code>, and create a new group "Trophies" for it in the "Menus" group:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_1.png" class="image"/>
			<br/>
			<p>
				Since the default resolution is <code>960x540</code> (as determined by 
				<router-link to="/docs/screen_width">screen_width</router-link> and
				<router-link to="/docs/screen_height">screen_height</router-link>),
				we'll make the new room that size using the Inspector, so it won't appear stretched or squashed:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_2.png" class="image"/>
			<br/>
			<p class="title">The Menu UI System</p>
			<p>
				All menu screens in the base engine use the Menu UI System.
				The first step to adding the Menu UI System to a room is to add an 
				<router-link to="/docs/obj_ui_runner">obj_ui_runner</router-link>
				instance to the room:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_3.png" class="image"/>
			<br/>
			<p>
				Next, add UI instances to create the desired menu layout (from the <code>[Engine] > Menu UI > Objects</code> group).
				For this tutorial, we'll make a simple layout with only a few buttons, labels, and images.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_4.png" class="image"/>
			<br/>
			<p>
				In order to customize the UI elements, double click on one and open the Variables tab.
				This will open up a list of properties for the individual instance that can be changed.
				For example, UI Buttons have a "text" property which determines what text they draw.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_5.png" class="image"/>
			<br/>
			<p>
				We'll come back to finish changing the properties later.
			</p>
			<br/>
			<p class="title">The UI Controller Object</p>
			<p>
				In order to allow the player to interact with the UI, we need a controller object to take player inputs and pass them to the UI cursors.
				Create a new object named <code>obj_trophies_ui</code>, and add the following code to the Room Start Event:
			</p>
			<pre>//MIS
mis_init();

//Universal Cursor
ui_cursor_add(0, room_width div 2, room_height div 2);

//Trophies
current_trophy = 0;
trophy_array =
	[
	spr_blocky_render,
	spr_poly_render,
	spr_vert_render,
	spr_scalar_render,
	spr_rad_render,
	];</pre>
			<p>
				This will initialize the <strong>Menu Input System</strong> (MIS) to handles player input, and create a single UI cursor that will be shared by all players and use the index <code>0</code>.
				Additionally, the variables <code>current_trophy</code> and <code>trophy_array</code> are initialized for later use.
			</p>
			<br/>
			<p>
				Add a Draw Event to <code>obj_trophies_ui</code>, and paste the following code:
			</p>
			<pre>//Draw the cursor
var _cursor = 0;
var _frame = 0;
if (ui_cursor_held_time(_cursor) > 0) then _frame = 1;
draw_sprite_ext(spr_menu_cursor, _frame, ui_cursor_x(_cursor), ui_cursor_y(_cursor), 1, 1, 0, c_white, 1);</pre>
			<p>
				This code simply draws the cursor, and changes the subimage based on if the cursor is clicking or not.
			</p>
			<br/>
			<p>
				Finally, add a Step Event and paste the following code:
			</p>
			<pre>//Get input from all MIS devices
var _confirm = false;
var _confirm_hold = 0;
var _gx = 0;
var _gy = 0;
var _array = mis_devices_get_array();
for(var i = 0; i &#60; array_length(_array); i++)
	{
	var _id = _array[@ i];
	var _values = mis_device_stick_values(_id);
	if (_values.hold > 0)
		{
		_gx += _values.x;
		_gy += _values.y;
		}
	if (!_confirm) then _confirm = mis_device_input(_id, MIS_INPUT.confirm);
	if (_confirm_hold == 0) then _confirm_hold = mis_device_input(_id, MIS_INPUT.confirm, true);
	}
	
var _x = ui_cursor_x(0);
var _y = ui_cursor_y(0);

//Cursor speed
var _dir = point_direction(0, 0, _gx, _gy);
var _len = min(point_distance(0, 0, _gx * menu_cursor_speed, _gy * menu_cursor_speed), menu_cursor_speed);
_gx = lengthdir_x(_len, _dir);
_gy = lengthdir_y(_len, _dir);

//Update Cursor
ui_cursor_update
	(
	0,
	clamp(_x + _gx, 0, room_width - 1),
	clamp(_y + _gy, 0, room_height - 1),
	false,
	_confirm,
	_confirm_hold,
	);</pre>
			<p>
				This is essentially a simpler version of the code in <router-link to="/docs/obj_sss_ui">obj_sss_ui</router-link>.
				It loops through all of the devices currently in the MIS, and sets the <code>_confirm</code> variable to <code>true</code> if any devices have "confirm" pressed
				(the specific button/key that counts as "confirm" is set in <router-link to="/docs/mis_init">mis_init</router-link>).
				It also gets the control stick values from each device and restricts it based on the <code>menu_cursor_speed</code>.
				Then, those values are used to update the UI cursor's position and "clicked" status.
			</p>
			<br/>
			<p>
				In order to test this, put an instance of <code>obj_trophies_ui</code> in the room, 
				then change the room order in the <strong>Room Manager</strong> so that <code>rm_trophies</code> is just below <code>rm_init</code>.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_6.png" class="image"/>
			<br/>
			<p>
				If done correctly, you should be able to move around the cursor like this:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_7.gif" class="image"/>
			<br/>
			<p class="title">Adding UI Scripts</p>
			<p>
				Now that all of the UI can be clicked on by the cursor, it's time to add functionality.
				This can be achieved by assigning scripts to the UI instances in the room.
			</p>
			<br/>
			<p>
				Create 5 new scripts named <code>trophies_ui_back_button_step</code>, <code>trophies_ui_next_button_step</code>, 
				<code>trophies_ui_previous_button_step</code>, <code>trophies_ui_label_step</code>, and 
				<code>trophies_ui_image_draw</code>.
			</p>
			<br/>
			<p>
				In <code>trophies_ui_back_button_step</code>, add the following code:
			</p>
			<pre>ui_button_step();
if (ui_clicked)
	{
	menu_sound_play(snd_menu_back);
	room_goto(rm_main_menu);
	}</pre>
			<p>
				The <code>ui_button_step</code> script is the default script buttons run, and it simply changes the button color when the button is clicked.
				The rest of the code checks if the button has been clicked, and returns to the main menu if so.
			</p>
			<br/>
			<p>
				Remember the <code>current_trophy</code> variable that was initialized in <code>obj_trophies_ui</code> earlier? 
				We'll have the "previous" and "next" buttons modify that variable. Paste the following code into <code>trophies_ui_previous_button_step</code>:
			</p>
			<pre>ui_button_step();
if (ui_clicked)
	{
	obj_trophies_ui.current_trophy -= 1;
	if (obj_trophies_ui.current_trophy &#60; 0)
		{
		obj_trophies_ui.current_trophy = array_length(obj_trophies_ui.trophy_array) - 1;
		}
	}</pre>
			<br/>
			<p>
				Now paste this code into <code>trophies_ui_next_button_step</code>:
			</p>
			<pre>ui_button_step();
if (ui_clicked)
	{
	obj_trophies_ui.current_trophy += 1;
	if (obj_trophies_ui.current_trophy >= array_length(obj_trophies_ui.trophy_array))
		{
		obj_trophies_ui.current_trophy = 0;
		}
	}</pre>
			<br/>
			<p>
				For the <code>trophies_ui_label_step</code> script, paste the following code:
			</p>
			<pre>text = sprite_get_name(obj_trophies_ui.trophy_array[@ obj_trophies_ui.current_trophy]);</pre>
			<p>
				This code will change the label to display the name of whatever "trophy" is currently being viewed.
			</p>
			<br/>
			<p>
				And finally, for <code>trophies_ui_image_draw</code>, paste this code:
			</p>
			<pre>var _spin = dcos(current_time / 50);
draw_sprite_ext(obj_trophies_ui.trophy_array[@ obj_trophies_ui.current_trophy], 0, x, y, _spin, 1, 0, c_white, 1);</pre>
			<p>
				This code draws the current "trophy", and spins it around slowly.
			</p>
			<br/>
			<p>
				Now that we have all of the scripts ready, it's time to assign them to the instances in the room through the Variables tab.
				When assigning scripts, make sure to <strong>NOT</strong> have parentheses at the end of the script name, 
				as that will cause the script to run instead of passing the script name.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_8.png" class="image"/>
			<br/>
			<img src="@/assets/Tutorials/ti4_9.png" class="image"/>
			<br/>
			<img src="@/assets/Tutorials/ti4_10.png" class="image"/>
			<br/>
			<p>
				Once you have all of the scripts assigned, run the project. It should look like this:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_11.gif" class="image"/>
			<br/>
			<p class="title">Adding buttons to the Main Menu</p>
			<p>
				Now that we have a custom menu screen up and running, we need to add it to the Main Menu so players can navigate to it in-game.
				This can be accomplished with a UI button, label, and a new script.
				Open <code>rm_main_menu</code> and add a new button and label to where you want:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_12.png" class="image"/>
			<br/>
			<p>
				Create a new script called <code>main_menu_ui_trophies_button_step</code>, and paste the following code:
			</p>
			<pre>ui_button_step();
if (ui_clicked)
	{
	menu_sound_play(snd_menu_select);
	room_goto(rm_trophies);
	}</pre>
			<p>
				Now, assign the script to the newly created button in the room.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_13.png" class="image"/>
			<br/>
			<p>
				Change the properties of the newly created label:
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_14.png" class="image"/>
			<br/>
			<p>
				Lastly, change the room order in the Room Manager so <code>rm_trophies</code> is no longer right after <code>rm_init</code>.
				Run the game, and you should be able to go to the trophies screen from the main menu!
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti4_15.gif" class="image"/>
			<br/>
			<p class="title">Extra notes</p>
			<p>
				This tutorial only covers a very small amount of the UI and MIS systems. 
				You can read the <router-link to="/docs">documentation</router-link> for more information.
			</p>
			<br/>
			<p>
				As you may have noticed, the trophies screen can't be accessed through the main menu sidebar on other screens.
				In order to add it to the sidebar, you'll need to change the code in <router-link to="/docs/main_menu_sidebar_choose">main_menu_sidebar_choose</router-link>
				and <router-link to="/docs/obj_main_menu_sidebar_ui">obj_main_menu_sidebar_ui</router-link>.
			</p>
			<br/>
			<p>
				If you wish to make a menu screen that doesn't use the cursor, you can use the MIS without UI cursors.
				The replays menu and the options menu are examples of this.
			</p>
			<br/>
			<p class="title">Summary</p>
			<p>
				- Each menu screen is a separate room resource
			</p>
			<p>
				- All menus use the <strong>Menu Input System</strong> (MIS), as well as the <strong>UI system</strong>
			</p>
			<p>
				- UI instances can be dragged into the room and customized through the Variables tab
			</p>
			<p>
				- You can give UI instances functionality using scripts and the Variables tab
			</p>
			<br/>
			<button class="button">
				<router-link to="/tutorials">
					Back to Tutorials
				</router-link>
			</button>
		</section>
	</div>
</template>