<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to set up Source Control (Github Desktop)
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">What is Source Control?</p>
      <p>
        Generally, "Source Control" refers to programs that <strong>keep track of all changes</strong> you make to files in a project and allow you to easily <strong>save and load backup versions</strong>.
      </p>
      <br/>
      <p>
        These features offer many benefits such as:
      </p>
      <ul>
        <li>- The ability to restore files you accidentally delete</li>
        <li>- Monitoring if any of your files have been corrupted</li>
        <li>- Quickly swapping between different versions of the project</li>
        <li>- Collaborating with multiple people on a project</li>
      </ul>
      <br/>
      <p class="title">Download</p>
      <p>
        There are many different Source Control programs you can choose from, but for this tutorial we'll use <strong>Github Desktop</strong>.
        Go to the Github Desktop download page at <a href="https://desktop.github.com/">https://desktop.github.com/</a>.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_1.png" class="image"/>
      <br/>
      <p>
        Run the <code>GitHubDesktopSetup-x64.exe</code> file, and follow all the instructions to install the program.
      </p>
      <br/>
      <p class="title">Adding PFE as a Repository</p>
      <p>
        Once you have Github Desktop fully set up on your computer, open the application and Add a Local Repository:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_2.png" class="image"/>
      <br/>
      <p>
        Select the folder your PFE project is saved in and give the repository a name.
        A prompt will appear at the bottom of the window asking if you'd like to Create a New Repository - click on the text to accept:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_3.png" class="image"/>
      <br/>
      <p>
        Make sure the name of your repository is what you want, then click on the Create repository button:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_4.png" class="image"/>
      <br/>
      <p>
        Navigate to the "History" tab and check that the project files are present in "Initial Commit":
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_5.png" class="image"/>
      <br/>
      <p>
        At this point everything is set up and ready to use! If you want to collaborate with others, or if you just want to save backups to the cloud, click on the "Publish repository" button.
        <strong>Make sure that "Keep this code private" is checked.</strong>
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_6.png" class="image"/>
      <br/>
      <p class="title">Tracking Changes and Making Commits</p>
      <p>
        Whenever you make a change to ANY files in the project folder, that change will be shown in Github Desktop.
        For example, if you changed Blocky's Forward Special to <code>basic_fspec_cloudburst</code>, it'll show this:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_7.png" class="image"/>
      <br/>
      <p>
        These changes are "not committed or pushed" yet, meaning they are only on your local computer and have not been sent to the cloud backup yet.
        Once you have enough changes that you are happy with, you can create a Commit by providing a name for the changes:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_8.png" class="image"/>
      <br/>
      <p>
        This will move all of the current changes to the "History" tab under the name of the Commit.
        The last step is to click on the "Push origin" button at the top, which will upload those changes to the cloud:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tp2_9.png" class="image"/>
      <br/>
      <p>
        And that's all there is to it!
        Even after changes are committed, you still have the option to "Revert changes in commit".
        You can also "Create branch from commit", which will make a new version of the entire project from that point in history.
        You can freely switch what branch you are working on, which can be useful when implementing new features or if you have multiple versions of your game
        (for example, a branch for the PC version and a branch for the Switch version).
      </p>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Source Control programs offer many useful features and should ALWAYS be used when working with PFE
      </p>
      <p>
        - Github Desktop is a popular free choice
      </p>
      <p>
        - In Github Desktop, file changes can be bundled into "Commits" and then "Pushed" to the cloud backup
      </p>
      <p>
        - You can create different "Branches" of the project and switch between them whenever you want
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>