<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Handling Player Input
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Types of Inputs</p>
      <p>
        Player inputs are collected from all controllers and keyboards, and then converted to values from the <code>INPUT</code> enum to be used in code.
        There are 9 inputs by default:
      </p>
      <ol>
        <li><code>attack</code>: Used for basic attacks such as jabs, tilts, dash attacks, ledge attacks, etc.</li>
        <li><code>special</code>: Used for special attacks.</li>
        <li><code>jump</code>: Used for jumps.</li>
        <li><code>shield</code>: Used for defensive options such as shields, parries, teching, etc.</li>
        <li><code>grab</code>: Used for grabs.</li>
        <li><code>smash</code>: Used for smash attacks, or aerial attacks.</li>
        <li><code>taunt</code>: Used for taunts.</li>
        <li><code>pause</code>: Used to pause the game.</li>
        <li><code>run</code>: Used to force the player to run instead of walk (mainly for keyboard users).</li>
      </ol>
      <br/>
      <p class="title">Checking Inputs</p>
      <p>
        There are 3 main functions that can be used to check inputs: 
        <router-link to="/docs/input_pressed">input_pressed</router-link>,
        <router-link to="/docs/input_held">input_held</router-link>, and
        <router-link to="/docs/input_reset">input_reset</router-link>.
      </p>
      <ol>
        <li><router-link to="/docs/input_pressed">input_pressed</router-link>:
        Returns <code>true</code> if the given input was pressed recently, and resets the input (unless the optional argument is set).</li>
        <li><router-link to="/docs/input_held">input_held</router-link>:
        Returns <code>true</code> if the given input is held down on the current frame.</li>
        <li><router-link to="/docs/input_reset">input_reset</router-link>:
        Resets the given input, so it won't trigger any other function until inputs are polled again the next frame.</li>
      </ol>
      <br/>
      <p class="title">Control Stick Checks</p>
      <p>
        Players also collect control stick inputs for both the <router-link to="/docs/Lstick">Lstick</router-link> and the <router-link to="/docs/Rstick">Rstick</router-link>
        (on keyboard, you can set which keys are used for each stick).
        These inputs can be accessed through the <code>stick_*</code> functions:
      </p>
      <ol>
        <li><router-link to="/docs/stick_tilted">stick_tilted</router-link>:
        Returns <code>true</code> if the given control stick is tilted in the given direction (from the <code>DIR</code> enum).</li>
        <li><router-link to="/docs/stick_flicked">stick_flicked</router-link>:
        Returns <code>true</code> if the given control stick was quickly moved in the given direction (from the <code>DIR</code> enum).</li>
        <li><router-link to="/docs/stick_direction">stick_direction</router-link>:
        Returns <code>true</code> if the given control stick is pointed in the given direction (from the <code>DIR</code> enum).</li>
        <li><router-link to="/docs/stick_get_direction">stick_get_direction</router-link>:
        Returns the direction the given control stick is pointed, in degrees.</li>
        <li><router-link to="/docs/stick_get_distance">stick_get_distance</router-link>:
        Returns the distance of the given control stick from the neutral position, which is a number from 0 to 1.</li>
        <li><router-link to="/docs/stick_get_value">stick_get_value</router-link>:
        Returns the value of the given control stick in the given direction, which will be between -1 and 1, depending on the direction (from the <code>DIR</code> enum).</li>
        <li><router-link to="/docs/stick_get_speed">stick_get_speed</router-link>:
        Returns how fast the given control stick has moved over the past few frames.</li>
        <li><router-link to="/docs/stick_check">stick_check</router-link>:
        A comprehensive function that returns <code>true</code> if there is a frame on which all of the stick conditions were met.</li>
        <li><router-link to="/docs/stick_find_frame">stick_find_frame</router-link>:
        Returns the frame number in which all of the conditions were met.</li>
        <li><router-link to="/docs/stick_choose_by_input">stick_choose_by_input</router-link>:
        Returns <router-link to="/docs/Rstick">Rstick</router-link> if the right stick is set to the given <code>INPUT</code>. 
        Otherwise, it returns <router-link to="/docs/Lstick">Lstick</router-link>.</li>
      </ol>
      <br/>
      <p class="title">Examples</p>
      <strong>Checking if the control stick was flicked up or down and the player pressed Attack:</strong>
      <pre>if (stick_flicked(Rstick, DIR.vertical) && input_pressed(INPUT.attack))
	{
	//Run code
	}</pre>
      <br/>
      <strong>Checking if the control stick is not tilted and the player is holding Shield:</strong>
      <pre>if (!stick_tilted(Lstick, DIR.any) && input_held(INPUT.shield))
	{
	//Run code
	}</pre>
      <br/>
      <strong>Checking if the player flicked the left stick upwards in the last 3 frames, and pressed Taunt in the last 4 frames:</strong>
      <pre>if (stick_flicked(Lstick, DIR.up, 3) && input_pressed(INPUT.taunt, 4))
	{
	//Run code
	}</pre>
      <br/>
      <strong>Setting the player's speed to move in the direction of the control stick if it is not in neutral:</strong>
      <pre>if (stick_tilted(Lstick, DIR.any))
	{
	var _dir = stick_get_direction(Lstick);
	var _len = 10;
	speed_set(lengthdir_x(_len, _dir), lengthdir_y(_len, _dir), false, false);
	}</pre>
      <br/>
      <p class="title">Summary</p>
      <p>
        - All controller and keyboard inputs are converted into <code>INPUT</code> values and stick values.
      </p>
      <p>
        - You can use the various <code>input_*</code> functions to handle player inputs.
      </p>
      <p>
        - You can use the various <code>stick_*</code> functions to handle control stick values.
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>