<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Explaining Hitboxes and Hurtboxes
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">What's the difference?</p>
      <p>
        Hitboxes are instances created during attacks that can collide with hurtboxes and cause effects such as damage and knockback.
        Hitboxes CANNOT collide with other hitboxes.
        Hurtboxes are instances that can be hit by hitboxes, and do not check for collisions themselves.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb4_3.png" class="image"/>
      <br/>
      <p>
        In this picture with <router-link to="/tutorialI1">debug mode</router-link> in use,
        the melee hitboxes are red and have arrows indicating which direction they launch opponents in.
        Both players have hurtboxes, which are drawn in green.
      </p>
      <br/>

      <p class="title">Hitbox Types</p>
      <p>
        There are 7 types of hitboxes available in the engine:
      </p>
      <br/>
      <ol>
        <li><router-link to="/docs/obj_hitbox_melee">obj_hitbox_melee</router-link>:
          A standard hitbox that is attached to the player and launches opponents
        </li>
        <li><router-link to="/docs/obj_hitbox_targetbox">obj_hitbox_targetbox</router-link>:
          Functions the same as a melee hitbox, except it can only hit a specified player (mainly used for throws)
        </li>
        <li><router-link to="/docs/obj_hitbox_magnetbox">obj_hitbox_magnetbox</router-link>:
          A hitbox that puts the opponent in the <code>magnetized</code> state, which moves them to a specific <code>x</code> and <code>y</code>.
          They are useful for multihit attacks and throws 
        </li>
        <li><router-link to="/docs/obj_hitbox_projectile">obj_hitbox_projectile</router-link>:
          A hitbox that is NOT attached to the player
        </li>
        <li><router-link to="/docs/obj_hitbox_grab">obj_hitbox_grab</router-link>:
          A special hitbox that grabs a single opponent on hit, 
          changing the attacking player to the <code>grabbing</code> state and the opponent to the <code>grabbed</code> state
        </li>
        <li><router-link to="/docs/obj_hitbox_windbox">obj_hitbox_windbox</router-link>:
          A hitbox that pushes opponents in a certain direction without changing their state
        </li>
        <li><router-link to="/docs/obj_hitbox_detectbox">obj_hitbox_detectbox</router-link>:
          A customizable hitbox that runs the <code>PHASE.detection</code> of the attack on hit, or a script
        </li>
      </ol>
      <br/>

      <p class="title">Hitbox Variables</p>
      <p>
        All hitbox objects are children of 
        <router-link to="/docs/obj_hitbox">obj_hitbox</router-link>
        and have these variables:
      </p>
      <br/>
      <ul>
        <li><strong>lifetime</strong>:
          The number of frames before the hitbox is destroyed
        </li>
        <li><strong>owner</strong>:
          The instance id of whatever created the hitbox
        </li>
        <li><strong>player_id</strong>:
          The instance id of the player who created the hitbox or owner
        </li>
        <li><strong>hitbox_group</strong>:
          For attached hitbox types. Only one hitbox from each group can hit an opponent
        </li>
        <li><strong>facing</strong>:
          The direction the player was facing when the hitbox was created. <code>1</code> is right, <code>-1</code> is left
        </li>
        <li><strong>has_hit</strong>:
          Whether the hitbox has hit an opponent yet
        </li>
        <li><strong>has_been_blocked</strong>:
          Whether the hitbox has been shielded by an opponent
        </li>
        <li><strong>can_be_parried</strong>:
          Whether the hitbox can be parried by the "Parry Press" or not (<code>SHIELD_TYPE.parry_press</code>)
        </li>
        <li><strong>check_first</strong>:
          Hitboxes are normally checked from oldest to newest, but setting this to true allows a newer hitbox to be checked before an older hitbox.
          This can be useful for attacks with sweetspots/sourspots
        </li>
        <li><strong>hit_vfx_style</strong>:
          The style of visual effect to create on hit. Must be from the enum <code>HIT_VFX</code>. 
          If the value is an array, it will create all of the visual effects in the array at the same time on hit
        </li>
        <li><strong>hit_sfx</strong>:
          The sound resource to play on hit
        </li>
        <li><strong>hit_restriction</strong>:
          A restriction for what types of opponents the hitbox can hit, from the enum <code>HIT_RESTRICTION</code>
        </li>
        <li><strong>pre_hit_script</strong>:
          A script that the hitbox's owner will run right before a hit happens
        </li>
        <li><strong>post_hit_script</strong>:
          A script that the hitbox's owner will run right after a hit happens
        </li>
        <li><strong>hurtbox_hit_list</strong>:
          A list used to store hurtbox instance ids when the hitbox is checking for collisions
        </li>
        <li><strong>destroy</strong>:
          Whether the hitbox should be destroyed at the end of the current frame or not
        </li>
      </ul>
      <br/>
      <img src="@/assets/Tutorials/tb4_1.png" class="image"/>
      <br/>
      <p>
        Each hitbox type also has its own set of available variables, which can be found in their Create Events.
      </p>
      <br/>

      <p class="title">Attached & Detached</p>
      <p>
        All hitboxes are either "attached" or "detached".
        Attached hitboxes move relative to the owner, put the owner in hitlag
        (the frames where a character appears to be "frozen" when an attack connects),  
        and are destroyed if the owner's attack is interrupted or finishes.
        Detached hitboxes do NOT move with the owner, do NOT put the owner in hitlag,
        and will continue to exist even after the owner's attack ends.
        Currently, <router-link to='/docs/obj_hitbox_projectile'>obj_hitbox_projectile</router-link>
        is the only detached hitbox, while all other hitbox types are attached.
      </p>
      <br/>

      <p class="title">Overlay Sprites</p>
      <p>
        Hitboxes are normally invisible, unless <router-link to="/docs/show_hitboxes">show_hitboxes</router-link> is turned on via <router-link to="/tutorialI1">debug mode</router-link>.
        If you want to give a hitbox a sprite that will be visible at all times, 
        you can use the <code>overlay_*</code> variables to set a sprite for the hitbox to draw.
        This is mainly intended for projectiles, but you can use it on any type of hitbox.
        The <code>overlay_*</code> variables are very similar to the <code>anim_*</code> variables for players.
      </p>
      <br/>
      <p>
        The <router-link to="/docs/hitbox_overlay_sprite_set">hitbox_overlay_sprite_set</router-link> function can be used to quickly
        set all of the <code>overlay_*</code> variables at once.
      </p>
      <br/>

      <p class="title">Hurtbox Types</p>
      <p>
        There are 3 types of hurtboxes (as seen in the <code>HURTBOX_TYPE</code> enum):
      </p>
      <br/>
      <ol>
        <li><strong>permanent</strong>:
          A hurtbox used for players or entities. 
          It will exist until the owner instance is destroyed.
          It can be created with <router-link to="/docs/hurtbox_create_permanent">hurtbox_create_permanent</router-link>
        </li>
        <li><strong>attack</strong>:
          A temporary hurtbox that only exists while the player is attacking (similar to attached hitboxes).
          It can be created with <router-link to="/docs/hurtbox_create">hurtbox_create</router-link>
        </li>
        <li><strong>shield</strong>:
          A temporary hurtbox for the <code>perfect_shield_start</code> / <code>parry_shield</code> shield types, if
          <router-link to="/docs/shield_poking_enable">shield_poking_enable</router-link> is set to <code>true</code>
        </li>
      </ol>
      <br/>
      <p class="title">Hurtbox Hit Scripts</p>
      <p>
        When a hitbox collides with a hurtbox, the hitbox calls the hurtbox's corresponding <strong>Hurtbox Hit Script</strong>.
        There is one hurtbox hit script for each type of hitbox.
      </p>
      <br/>
      <p>
        Hurtbox hit scripts generally handle invulnerability, damage, knockback, visual effects, etc.
        The <router-link to="/docs/hurtbox_hit_script_template">hurtbox_hit_script_template</router-link> 
        script can be duplicated to create new hurtbox hit scripts.
      </p>
      <p>
        The function <router-link to="/docs/hurtbox_setup">hurtbox_setup</router-link> 
        can be used to assign hurtbox hit scripts to a specific hurtbox instance.
        By default, hurtboxes will use these scripts:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb4_2.png" class="image"/>
      <br/>
      <p>
        Hurtbox hit scripts are incredibly useful for entities, since you may not want them to follow the default behavior when getting hit.
        For example, <code>obj_pacman_dspec_hydrant</code> uses custom hit scripts that causes it to only be launched after taking a certain amount of damage.
      </p>
      <br/>

      <p class="title">Summary</p>
      <p>
        - Hitboxes are special instances that can only collide with hurtboxes, and run code during the collision
      </p>
      <p>
        - There are 7 types of hitboxes and 3 types of hurtboxes
      </p>
      <p>
        - Each type of hitbox inherits variables from <router-link to="/docs/obj_hitbox">obj_hitbox</router-link>, while also having unique variables
      </p>
      <p>
        - You can assign an overlay sprite to a hitbox, which will be drawn even if <router-link to="/docs/show_hitboxes">show_hitboxes</router-link> is <code>false</code>
      </p>
      <p>
        - Hurtboxes have <strong>Hurtbox Hit Scripts</strong>, which are ran when a hitbox collides with them
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>