<template>
  <div>
    <div class="tile is-ancestor">
      <div class="tile is-parent is-6 is-vertical">
        <div class="box tile is-child" style="flex-grow: 0">
          <div class="title">{{ $route.params.name }}</div>
          <div class="subtitle">
            <router-link :to="'/docs/functions'">
              Function 
            </router-link>
            | {{ documentation_data[$route.params.name].c }}
          </div>
        </div>
        <div class="box tile is-child">
          <div class="subtitle"><strong>Description</strong></div>
          <div class="description">
            <div class="description" v-html="documentation_data[$route.params.name].d"/>
          </div>
        </div>
      </div>
      <div class="tile is-parent">
        <div class="box tile is-child">
          <div class="subtitle"><strong>Arguments</strong></div>
          <table class="table is-fullwidth is-striped">
            <thead v-if="documentation_data[$route.params.name].p.length > 0">
              <tr>
                <th>Name</th>
                <th>Data Type</th>
                <th>Description</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="param in documentation_data[$route.params.name].p" :key="param">
                <td>{{ param.n }}</td>
                <td><i>{{ param.t }}</i></td>
                <td><div class="description" v-html="param.d"/></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div >
      <button class="button" @click="show_related_functions ^= true"><strong>Related Functions</strong></button>
      <div v-show="show_related_functions">
        <br/>
        <div v-for="func in function_categories[documentation_data[$route.params.name].c]" :key="func">
          <router-link :to="'/docs/' + documentation_data[func].n">
            {{ documentation_data[func].n }}
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocFunction",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    function_categories: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      show_related_functions: false,
    }
  },
  //created() {
  //  window.scrollTo(0, 0);
  //}
};
</script>