<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to use Debug Mode
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Enabling Debug Mode</p>
      <p>
        You can enable Platform Fighter Engine's "debug mode" through the Options menu in-game.
      </p>
      <br/>
      <p>
        Having debug mode enabled allows you to use:
      </p>
      <ol>
        <li>Function key overlays</li>
        <li>Live value reloading</li>
        <li>The <router-link to="/docs/log">log</router-link> function</li>
      </ol>
      <br/>
      <p class="title">Function Keys</p>
      <p>
        During gameplay, you can press the function keys to toggle debug overlays or perform debug functions.
      </p>
      <br/>
      <ul>
        <li>
          <code> F1</code> Toggles the <strong>dynamic resource</strong> counter, which shows you the numbers of specific dynamic resource that are being stored in memory.
          In general, these numbers should not increase after consecutive games, otherwise a memory leak may be present.
        </li>
        <li>
          <code> F2</code> Calls <code>show_debug_overlay</code>, a GameMaker function that draws a performance bar at the top of the screen.
          The official GameMaker docs explain the meaning of each part of the bar.
        </li>
        <li>
          <code> F3</code> Toggles the <strong>input buffer</strong> graph, which shows the value of each input for every player's input buffer.
          The further each bar reaches down, the longer it has been since that input was pressed. 
          Please note: Some inputs are "eaten" by the game, that is, instantly set back to the maximum time after being detected. This means not all inputs will make a visible change to the bars.
        </li>
        <li>
          <code> F4</code> Displays debug values on top of every player.
          This includes the player's coordinates, last 5 states in the state log, and the coordinates of their left control stick.
        </li>
        <li>
          <code> F5</code> Makes all hitboxes visible by changing <router-link to="/docs/show_hitboxes">setting().show_hitboxes</router-link>.
        </li>
        <li>
          <code> F6</code> Makes all hurtboxes visible by changing <router-link to="/docs/show_hurtboxes">setting().show_hurtboxes</router-link>.
        </li>
        <li>
          <code> F7</code> Makes all player collision masks visible by changing <router-link to="/docs/show_collision_boxes">setting().show_collision_boxes</router-link>.
        </li>
        <li>
          <code> F8</code> Displays launch trajectories for attacks by changing <router-link to="/docs/show_launch_trajectories">setting().show_launch_trajectories</router-link>
        </li>
        <li>
          <code> F9</code> Toggles the <strong>game state instance</strong> counter, which shows you the numbers of specific instances that are necessary for the game state.
          This is only intended for debugging netcode.
        </li>
        <li>
          <code> F10</code> Prints out the results of <code>gc_get_stats</code> to the GameMaker output.
        </li>
        <li>
          <code> F11</code> Reloads the values from the <code>live_values.json</code> file in the local app data.
          These values can be accessed through the <router-link to="/docs/live_value">live_value</router-link> function,
          and can be useful for speeding up development since you won't have to recompile the game to change specific values.
        </li>
        <li>
          <code> F12</code> Turn on, or change the color of the green screen.
        </li>
      </ul>
      <br/>
      <p class="title">The Log Function</p>
      <p>
        Turning on debug mode while <router-link to="/docs/show_debug_logs">show_debug_logs</router-link> is <code>true</code> allows the 
        <router-link to="/docs/log">log</router-link> function to print to the GameMaker output.
        There are various parts of the engine that already use <router-link to="/docs/log">log</router-link>, so enabling it may be useful for debugging issues with the engine.
      </p>
      <br/>
      <p class="title">FPS Debug Overlay</p>
      <p>
        You can turn on the FPS debug overlay by changing <router-link to="/docs/debug_fps">setting().debug_fps</router-link> to <code>true</code>.
        This displays the estimated min, max, and average FPS values during a single match.
      </p>
      <br/>
      <p class="title">Sync Test</p>
      <p>
        You can turn on sync testing by changing <router-link to="/docs/debug_sync_test">setting().debug_sync_test</router-link> to <code>true</code>.
        This is ONLY intended for testing purposes, and should never be turned on in the final product, as it will greatly decrease performance.
      </p>
      <br/>
      <p>
        While sync testing is turned on, the game will save and load every single frame, and then check for desyncs.
        This can be used to ensure the game state saves and loads properly for online matches, without needing to play against someone in a real online match.
      </p>
      <br/>
      <p class="title">Summary</p>
      <p>
        - You can turn on debug mode with <router-link to="/docs/debug_mode_enable">setting().debug_mode_enable</router-link>
      </p>
      <p>
        - Use the function keys to toggles different debug overlays
      </p>
      <p>
        - You can turn on the FPS overlay with <router-link to="/docs/debug_fps">setting().debug_fps</router-link>
      </p>
      <p>
        - You can turn on sync testing with <router-link to="/docs/debug_sync_test">setting().debug_sync_test</router-link>, but ONLY do it for testing
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>