<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to add a New Input
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Input Enums</p>
      <p>
        All inputs in PFE must be defined in the correct enums. The reason why enums are used instead of macros or strings is that they
        have integer values that won't overlap, are easy to iterate through, and have better autocorrect in the GameMaker IDE.
        These can be modified in the <router-link to="/docs/enums">enums</router-link> script:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_1.png" class="image"/>
      <br/>
      <p>
        Any new inputs must be added to <strong>4</strong> enums: <code>INPUT</code>, <code>CC_INPUT_CONTROLLER</code>, <code>CC_INPUT_KEYBOARD</code>, and <code>CC_INPUT_TOUCH</code>.
      </p>
      <br/>
      <p>
        The <code>INPUT</code> enum is for all inputs that are directly used in-game, while the other enums are for custom controls.
        All of the custom controls enums need to start with the same entries from the <code>INPUT</code> enum to make sure everything lines up correctly.
        However, the custom controls enums can have different entries after that - for example, if you wanted touch controls to have a wavedash button, but you didn't want
        controller or keyboard to have it, you would only add it to the <code>CC_INPUT_TOUCH</code> enum and nothing else.
      </p>
      <br/>
      <p class="title">EX Input</p>
      <p>
        For this tutorial, we are going to add an "EX" input. When holding this down, players will use EX versions of moves when possible.
        This means players won't have to press Special and Attack at the same time to use EX moves, which is how it works in the base engine.
      </p>
      <br/>
      <p>
        The first step is to add the "EX" input to all 4 of the enums in the same spot, right after the "run" input:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_2.png" class="image"/>
      <br/>
      <p class="title">Input Scripts</p>
      <p>
        There are 2 scripts that detect player inputs - <router-link to="/docs/game_local_input_write">game_local_input_write</router-link> for when the game is running, and
        <router-link to="/docs/input_paused_collect">input_paused_collect</router-link> for Frame Advance.
        Now for this tutorial, we do NOT need to edit these scripts because we are adding a gameplay input, but if we were adding a custom controls input, both of these scripts would have to be modified.
      </p>
      <br/>
      <p>
        Additionally, the <router-link to="/docs/input_update">input_update</router-link> script may need to be modified for certain inputs.
        For example, the "run" input is checked in this script and changes the player's left stick values before they can be used by any other code.
      </p>
      <br/>
      <p class="title">Checking the Input In-Game</p>
      <p>
        To check the input in-game, we'll open the <code>ex_move_allow</code> script and change the <router-link to="/docs/input_held">input_held</router-link> call to
        check <code>INPUT.ex</code> instead.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_3.png" class="image"/>
      <br/>
      <p>
        Technically, this is all you need to do to add a new input...except players aren't able to press the input right now, because there is no way to map buttons to the input.
        The first script to change is <router-link to="/docs/custom_controls_default">custom_controls_default</router-link>, so all newly-created profiles will have buttons mapped to the "EX" input.
        You can decide what the "EX" input should be for controller, keyboard, and touch controls, or choose to not have anything mapped to the "EX" input by default.
        For this tutorial, we will map the Left Trigger of controllers and the Ctrl key on keyboards to the "EX" input, and leave out touch controls.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_4.png" class="image"/>
      <br/>
      <p>
        You can test this out by running the game and using the autogenerated "PLAYER 1" profile. Turn on the EX meter, and go into a match with the CPU. You should be able to use EX moves by holding the
        button or key you set. Additionally, if you have <strong>Debug Mode</strong> turned on, you can press <code>F3</code> to display the input buffer graph, which will show when the new input is being pressed or held:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_5.png" class="image"/>
      <br/>
      <p class="title">Adding to the Custom Controls Menu</p>
      <p>
        To add the new input to the custom controls menu, we need to give the input a proper name for menus.
        Open the <router-link to="/docs/input_name_get">input_name_get</router-link> and <router-link to="/docs/cc_input_name_get">cc_input_name_get</router-link> scripts,
        and add lines for the new input:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_6.png" class="image"/>
      <br/>
      <br/>
      <img src="@/assets/Tutorials/ti5_7.png" class="image"/>
      <br/>
      <p>
        Next, we need to make an icon for the input. Open <code>spr_icon_inputs</code> and add a new subimage after the "run" icon.
        For this tutorial, the subimage will simply have the letters "EX".
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_8.png" class="image"/>
      <br/>
      <p>
        If you run the game now, you'll be able to edit the custom controls of a profile and map anything to the "EX" input.
        <span class='please_note'>Please note:</span> If you are using a profile that was created BEFORE the new input was added, some of the controls might be messed up on it.
      </p>
      <br/>
      <img src="@/assets/Tutorials/ti5_9.png" class="image"/>
      <br/>
      <p class="title">The Default Profiles</p>
      <p>
        The project comes with an included datafile called <code>default_profiles.sav</code>. This is used if there are no saved profiles detected on the computer, 
        or if the profiles cannot be read for some reason.
        When a new input is added or removed, this file needs to be updated.
        You can manually update it by editing the JSON <i>outside</i> of the GameMaker IDE, or edit the profiles in-game and then copy your <code>profiles.sav</code> file from your Local App Data folder to <code>default_profiles.sav</code>.
      </p>
      <br/>
      <p class="title">Summary</p>
      <p>
        - All inputs must be defined in the correct enums
      </p>
      <p>
        - Depending on the type of input you want to add, you may need to update some of input scripts
      </p>
      <p>
        - Inputs need to be given names and icons for the Custom Controls Menu
      </p>
      <p>
        - The <code>default_profiles.sav</code> file must be updated whenever inputs are added or removed
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>