<template>
  <div>
    <div class="title">List of all Global Constants</div>
    <input class="input block" type="text" placeholder="Filter..." @change="filter_all($event.target.value)"/>
    <button class="button block mr-3" @click="show_types ^= true"><strong>Show Types</strong></button>
    <button class="button block" @click="show_defaults ^= true"><strong>Show Defaults</strong></button>
    <div class="table-container table_rounded">
      <table class="table is-fullwidth is-striped">
        <thead>
          <tr>
            <th>Name</th>
            <th v-show="show_types">Type</th>
            <th v-show="show_defaults">Default Value</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="macro in macro_names" :key="macro" :id="macro" :class="{ 'has-background-grey': $route.params.name == macro, 'has-text-black': $route.params.name == macro }">
            <td v-show="string_contains(macro, filter_value)">{{ documentation_data[macro].n }}</td>
            <td v-show="string_contains(macro, filter_value) && show_types"><i>{{ documentation_data[macro].t }}</i></td>
            <td v-show="string_contains(macro, filter_value) && show_defaults"><code>{{ documentation_data[macro].v }}</code></td>
            <td v-show="string_contains(macro, filter_value)"><div class="description" v-html="documentation_data[macro].d"/></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocMacro",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    macro_names: {
      type: Array,
      required: true,
    }
  },
  data() {
    return { 
      show_types: false,
      show_defaults: false,
      filter_value: "",
      };
  },
  methods: {
    filter_all(value) {
      this.filter_value = value;
      console.log(this.filter_value);
    },
    string_contains(string, value) {
      //Blank string means no filter is set
      return (value == "" ? true : string.includes(value));
    },
  }
};
</script>

<style scoped>
  .table_rounded {
    border-radius: 6px;
  }
</style>