<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to add a New Attack
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Attack Scripts</p>
      <p>
        All attacks in PFE are represented by an <strong>Attack Script</strong>. 
        When a player starts an attack, their <code>attack_script</code> variable is set to the corresponding value in the <code>my_attacks</code> struct.
        During the attack, the <code>attack_script</code> will be run <i>every frame</i>. 
        This means attack scripts need to cover all phases of an attack - startup, active, and endlag.
      </p>
      <br/>
      <p>
        There are many possible ways to set up attack scripts, but this tutorial will cover the way most pre-made attacks are built.
      </p>
      <br/>

      <p class="title">Attack Phases and Frames</p>
      <p>
        Players have two variables by default that can be used for attack timings - <code>attack_phase</code> and <code>attack_frame</code>.
        The <code>attack_phase</code> stores a number representing the current "phase" of the attack.
        On a simple attack such as <code>chrom_jab</code>, phase 0 is the "startup", phase 1 is the "active", and phase 2 is the "endlag".
        On more complicated attacks, there could be phases for charging, multihits, grounded/aerial versions, etc.
      </p>
      <br/>
      <p>
        The <code>attack_frame</code> variable stores which frame the player is on in the current <code>attack_phase</code>.
        By default, the <code>attack_frame</code> counts DOWN.
        So an attack with 5 frames of startup would set <code>attack_frame = 5;</code> at the start, and then wait until the frame is equal to 0 before going to the next phase.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_1.png" class="image"/>
      <br/>
      
      <p class="title">Animations</p>
      <p>
        There are multiple ways to code attack animations in PFE.
        Most of the pre-made attacks manually animate, meaning the attack script sets the <code>anim_frame</code> on certain frames of the attack.
        The benefit of this method is that it gives complete control over the animation, so you can guarantee the sprite <i>always</i> matches up with the hitboxes.
      </p>
      <br/>
      <p>
        In the code for <code>chrom_jab</code>, you can see that the <code>anim_frame</code> is set to 1 during the startup, and 2 exactly when the hitbox is created.
        Subimage 2 in the sprite is the start of the sword swing (remember that subimages start from 0).
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_2.png" class="image"/>
      <br/>
      <p>
        If you don't want to manually animate attacks, you can set the <code>anim_speed</code>, which will automatically change the <code>anim_frame</code>.
        The helper function <router-link to="/docs/anim_calculate_speed">anim_calculate_speed</router-link> can be used to calculate the <code>anim_speed</code> needed for the animation to finish in a certain number of frames.
      </p>
      <br/>

      <p class="title">Movement and Cancels</p>
      <p>
        Most attack scripts have code outside the phase <code>switch</code> statement to handle movement and cancels.
      </p>
      <br/>
      <p>
        The movement function is typically at the very end of the attack to ensure any speed changes that happen during the attack are taken into account.
        There are four movement functions that attacks can use:
      </p>
      <ol>
        <li><router-link to="/docs/move">move</router-link>: Moves the player on the ground or in the air</li>
        <li><router-link to="/docs/move_grounded">move_grounded</router-link>: Moves the player without going off edges horizontally. If the player is in the air, they won't move horizontally at all</li>
        <li><router-link to="/docs/move_hit_platforms">move_hit_platforms</router-link>: Moves the player on the ground or in the air, but the player cannot drop through platforms</li>
        <li><router-link to="/docs/move_through_platforms">move_through_platforms</router-link>: Moves the player on the ground or in the air, and the player cannot land on platforms</li>
      </ol>
      <br/>
      <p>
        To control exactly how far the player moves, there are various functions to set the player's speeds:
      </p>
      <ol>
        <li><router-link to="/docs/speed_set">speed_set</router-link>: Directly set the player's horizontal and vertical speeds</li>
        <li><router-link to="/docs/friction_gravity">friction_gravity</router-link>: Applies friction and gravity values to the player</li>
        <li><router-link to="/docs/aerial_drift">aerial_drift</router-link>: Allows players to drift using their character's <code>air_accel</code></li>
        <li><router-link to="/docs/fastfall_attack_try">fastfall_attack_try</router-link>: Allows players to fastfall</li>
        <li><router-link to="/docs/allow_hitfall">allow_hitfall</router-link>: Allows players to fastfall when hitting an opponent</li>
      </ol>
      <br/>
      <p>
        There are a few "cancel" functions that can be used to determine when attacks are stopped. The most common ones are:
      </p>
      <ol>
        <li><router-link to="/docs/cancel_air_check">cancel_air_check</router-link>: Automatically cancels the attack if the player is in the air. This should be included in all grounded attacks</li>
        <li><router-link to="/docs/cancel_ground_check">cancel_ground_check</router-link>: Automatically cancels the attack if the player is on the ground. This should be included in all aerial attacks</li>
      </ol>
      <br/>
      <p>
        Make sure to set the local var <code>run</code> to <code>false</code> if a cancel function returns <code>true</code>, so the attack will NOT run the code for phases!
      </p>
      <br/>

      <p class="title">Hitboxes</p>
      <p>
        Players can create hitboxes using any of the <code>hitbox_create_*</code> functions.
        Most attacks use <router-link to="/docs/hitbox_create_melee">hitbox_create_melee</router-link>, which creates a hitbox that is attached to the player and launches opponents.
        The hitbox creation functions have arguments for many hitbox variables, but if you need to access other variables, you can store the instance id of the newly created hitbox in a local var, and use the dot operator:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_3.png" class="image"/>
      <br/>
      <p>
        You can look in the Create Event of a hitbox object to see what variables it has. Every hitbox object shares the variables that <router-link to="/docs/obj_hitbox">obj_hitbox</router-link> has.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_4.png" class="image"/>
      <br/>

      <p class="title">Creating a New Attack: Joker's Forward Aerial</p>
      <p>
        Now that we've covered the basics of attack scripts, let's go through the process of creating an aerial.
        To start, duplicate <code>mario_fair</code> and rename it <code>joker_fair</code>. 
        Make sure to rename the script <i>and</i> the function inside the script!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_5.png" class="image"/>
      <br/>
      <p>
        Import the following sprite and name it <code>spr_joker_fair</code>:
      </p>
      <br/>
      <div class="card">
        <header class="card-header"><p class="card-header-title">spr_joker_fair_strip13</p></header>
        <div class="card-image">
          <div class="section"><img src="@/assets/Tutorials/spr_joker_fair_strip13.png" class="image"/></div>
        </div>
      </div>
      <br/>
      <img src="@/assets/Tutorials/tb2_22.png" class="image"/>
      <br/>
      <p>
        Make sure to set the Origin of the sprite to <code>(32, 19)</code>, and the texture group to <code>texture_shared</code> (or to a specific character's texture group if you only want it to be used by one character).
        You can check the <router-link to="/docs/character_data">character_data</router-link> script to see what texture group(s) are assigned to each character.
      </p>
      <br/>
      <p>
        Since we're copying an attack from Smash Ultimate, we can find the frame data <a href="https://ultimateframedata.com/joker.php" target="_blank" rel="noopener">here</a>.
      </p>
      <br/>
      <p>
        Now, back to the attack script.
        To make the script use the sprite, set <code>anim_sprite = spr_joker_fair;</code> at the start.
        The attack has a 7 frame startup and 12 frames of landing lag, change the code at the start to reflect those values:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_6.png" class="image"/>
      <br/>
      <p>
        The attack doesn't need the minor vertical boost or the shine effect, so you can delete those lines if you wish.
      </p>
      <br/>
      <p>
        Now for the startup phase. There are only two subimages in the sprite before the first subimage that would need a hitbox, 
        and since the <code>anim_frame</code> was already set to 0 at the start, we only need to write code to change it to 1.
        In this example, the player will change subimage when there are 3 frames of startup left.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_7.png" class="image"/>
      <br/>
      <p>
        The next part is adding the first hitbox. 
        You can use the <code>hitbox_planner</code> sequence to help position the hitboxes, but you'll have to type the values into the script at the end.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_8.png" class="image"/>
      <br/>
      <img src="@/assets/Tutorials/tb2_9.png" class="image"/>
      <br/>
      <p>
        Remember to also change the <code>anim_frame</code>!
      </p>
      <br/>
      <p>
        The first hit is an extremely weak hit that should combo into the second hit, so we'll give the hitbox low damage, low knockback, and low scaling.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_10.png" class="image"/>
      <br/>
      <p>
        In this example, the hitbox only deals 2 damage. 
        The base knockback is 5, and the scaling is 0, so the knockback won't increase even if the opponent has high damage.
        There are 4 frames of hitlag, so the hit feels weak.
        The knockback angle when facing right is 75, a relatively steep angle that should combo well.
        The hitbox lasts for 2 frames, just like in the original attack, and uses a circular hitbox shape.
        Lastly, the hitbox is part of hitbox group 0, which will be important later on.
      </p>
      <br/>
      <p>
        Next, we'll add in the space between the hits. There should be 5 frames between the hits:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_11.png" class="image"/>
      <br/>
      <p>
        Side Note: You may see a GameMaker warning on this line.
        "Variable only referenced once" warnings can be ignored, but if you want it to go away you can remove the <code>var _hitbox = </code> part of the code.
        You'll need the <code>_hitbox</code> variable if you want to change other properties of the hitbox though, so it's convenient to leave the variable in the code.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_23.png" class="image"/>
      <br/>
      <p>
        For the inbetween phase, we'll need to replace the existing code to animate the attack properly...
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_12.png" class="image"/>
      <br/>
      <p>
        Now, it's time to add the second hitbox!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_13.png" class="image"/>
      <br/>
      <p>
        As you can see, the second hitbox is larger and stronger than the first.
        It has more damage, higher base knockback, and higher scaling.
      </p>
      <br/>
      <p>
        Additionally, it has a knockback angle of 0. This is actually a placeholder value - 
        the knockback flipper at the end, <code>FLIPPER.sakurai</code>, will override it during gameplay.
        If you don't know what the sakurai angle is, you can read the <a href="https://www.ssbwiki.com/Sakurai_angle" target="_blank" rel="noopener">SmashWiki article</a> on it.
      </p>
      <br/>
      <p>
        The hitbox group for the second hitbox is 1, instead of 0.
        Hitboxes in the same group <i>cannot</i> hit the same opponent twice, so the second hitbox needs to be in a different group if the attack has multiple hits.
        By default there are only 4 hitbox groups available (0 to 3), but there are functions to reset hitbox groups during gameplay
        (<router-link to="/docs/hitbox_group_reset">hitbox_group_reset</router-link>), and you can change the number of hitbox groups available for the whole project with the setting
        <router-link to="/docs/hitbox_groups_max">hitbox_groups_max</router-link>.
      </p>
      <br/>
      <p>
        Now that we've got both hitboxes set up, the only thing left is to finish the animation and the endlag state.
        The actual attack has ~35 frames of endlag, but we're going to decrease it to 25 to make it more similar to other attacks in PFE.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_14.png" class="image"/>
      <br/>
      <p>
        Here's what the full script should look like now:
      </p>
      <br/>
      <pre>function joker_fair()
	{
	//Forward Aerial
	var run = true;
	var _phase = argument_count > 0 ? argument[0] : attack_phase;
	//Timer
	attack_frame = max(--attack_frame, 0);
	//Actions
	friction_gravity(air_friction, grav, max_fall_speed);
	fastfall_attack_try();
	allow_hitfall();
	aerial_drift();
	//Cancels
	if run && cancel_ground_check()	run = false;
	//Main Phases
	if (run)
		{
		switch(_phase)
			{
			case PHASE.start:
				{
				anim_sprite = spr_joker;
				anim_frame = 0;
				anim_speed = 0;
				landing_lag = 12;
				attack_frame = 7;
				return;
				}
			//Startup
			case 0:
				{
				//Animation
				if (attack_frame == 3)
					anim_frame = 1;
  
				//First hit
				if (attack_frame == 0)
					{
					anim_frame = 2;
					
					var _hitbox = hitbox_create_melee(18, 18, 0.9, 0.5, 2, 5, 0, 4, 75, 2, SHAPE.circle, 0);
					
					//Next phase
					attack_phase++;
					attack_frame = 5;
					}
				break;
				}
			//Active / Inbetween
			case 1:
				{
				//Animation
				if (attack_frame == 4)
					anim_frame = 3;
				if (attack_frame == 3)
					anim_frame = 4;
				if (attack_frame == 2)
					anim_frame = 5;
				if (attack_frame == 1)
					anim_frame = 6;
	
				//Second hit
				if (attack_frame == 0)
					{
					anim_frame = 7
					
					var _hitbox = hitbox_create_melee(32, 4, 0.9, 0.6, 5, 7, 0.9, 8, 0, 2, SHAPE.circle, 1, FLIPPER.sakurai);
					
					//Next phase
					attack_phase++;
					attack_frame = 25;
					}
				break;
				}
			//Endlag
			case 2:
				{
				//Animation
				if (attack_frame == 23)
					anim_frame = 8;
				if (attack_frame == 18)
					anim_frame = 9;	
				if (attack_frame == 13)
					anim_frame = 10;
				if (attack_frame == 8)
					anim_frame = 11;	
				if (attack_frame == 4)
					anim_frame = 12;	
					
				if (attack_frame == 0)
					{
					//Revert back to the original state
					attack_stop(PLAYER_STATE.aerial);
					}
				break;
				}
			}
		}
	//Movement
	move();
	}
/* Copyright 2024 Springroll Games / Yosi */</pre>
      <br/>
      <p>
        To test the attack, open a character init script and assign the <code>joker_fair</code> attack script to the character's forward aerial.
        Make sure to NOT put parentheses after the script name.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_15.png" class="image"/>
      <br/>
      <p>
        Now run the game and try out the new attack!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_16.gif" class="image"/>
      <br/>

      <p class="title">Extras</p>
      <p>
        The framework of the attack is finished, but there are still a lot of extra elements that can be added.
        For the rest of this tutorial, we'll go over Visual / Sound Effects and Hurtbox Matching.
      </p>
      <br/>
      <p>
        Setting Visual and Sound Effects for hitboxes can easily be done using the dot operator.
        In <code>joker_fair</code>, we used the local var <code>_hitbox</code> to store the instance id of each hitbox as it was created,
        so we can set the visual effect using <code>_hitbox.hit_vfx_style</code> and the sound effect using <code>_hitbox.hit_sfx</code>.
        Visual effects must be from the <code>HIT_VFX</code> enum. Sound effects can be any sound resources imported into GameMaker.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_18.png" class="image"/>
      <br/>
      <img src="@/assets/Tutorials/tb2_19.png" class="image"/>
      <br/>
      <p>
        Now onto Hurtbox Matching. 
        If you got the chance to play around with <code>joker_fair</code> a bit, you might notice that the attacking player's hurtbox is, well, completely inaccurate.
        (When debug mode is turned on from the in-game menu, you can press <code>F6</code> to turn on hurtboxes during a match).
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_20.png" class="image"/>
      <br/>
      <p>
        Fortunately, this is pretty easy to fix if we make a separate sprite for the hurtbox:
      </p>
      <br/>
      <div class="card">
        <header class="card-header"><p class="card-header-title">spr_joker_fair_hurtbox_strip13</p></header>
        <div class="card-image">
          <div class="section"><img src="@/assets/Tutorials/spr_joker_fair_hurtbox_strip13.png" class="image"/></div>
        </div>
      </div>
      <br/>
      <p>
        Once you've imported that sprite, name it <code>spr_joker_fair_hurtbox</code>. Make sure to set the Origin, texture group, and collision mask!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_24.png" class="image"/>
      <br/>
      <p>
        In the <code>joker_fair</code> script, add the line <code>hurtbox_anim_match(spr_joker_fair_hurtbox);</code> at the end of the script,
        right after the movement function.
      </p>
      <br/>
      <p>
        If you run the game again, you can see that the player's hurtbox will now properly match the animation during the attack.
        But if you look closely, the hurtbox will continue to use the <code>spr_joker_fair_hurtbox</code> sprite even after the attack finishes.
        This is because the <router-link to="/docs/hurtbox_anim_match">hurtbox_anim_match</router-link> function is being run even if the attack is canceled or stopped earlier in the script.
      </p>
      <br/>
      <p>
        To fix this, we can use the <code>run</code> local var. Add the condition <code>if (run)</code> to <code>hurtbox_anim_match(spr_joker_fair_hurtbox);</code>,
        and then add <code>run = false;</code> right after the <code>attack_stop(PLAYER_STATE.aerial);</code> line:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_21.png" class="image"/>
      <br/>
      <p>
        When you run the game again, the hurtbox will no longer last after the attack finishes.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb2_22.gif" class="image"/>
      <br/>
      <p class="title">The Full Code</p>
      <pre>function joker_fair()
	{
	//Forward Aerial
	var run = true;
	var _phase = argument_count > 0 ? argument[0] : attack_phase;
	//Timer
	attack_frame = max(--attack_frame, 0);
	//Actions
	friction_gravity(air_friction, grav, max_fall_speed);
	fastfall_attack_try();
	allow_hitfall();
	aerial_drift();
	//Cancels
	if run && cancel_ground_check()	run = false;
	//Main Phases
	if (run)
		{
		switch(_phase)
			{
			case PHASE.start:
				{
				anim_sprite = spr_joker_fair;
				anim_frame = 0;
				anim_speed = 0;
				landing_lag = 12;
				attack_frame = 7;
				return;
				}
			//Startup
			case 0:
				{
				//Animation
				if (attack_frame == 3)
					anim_frame = 1;
  
				//First hit
				if (attack_frame == 0)
					{
					anim_frame = 2;
					
					var _hitbox = hitbox_create_melee(18, 18, 0.9, 0.5, 2, 5, 0, 4, 75, 2, SHAPE.circle, 0);
					
					//Next phase
					attack_phase++;
					attack_frame = 5;
					}
				break;
				}
			//Active / Inbetween
			case 1:
				{
				//Animation
				if (attack_frame == 4)
					anim_frame = 3;
				if (attack_frame == 3)
					anim_frame = 4;
				if (attack_frame == 2)
					anim_frame = 5;
				if (attack_frame == 1)
					anim_frame = 6;
	
				//Second hit
				if (attack_frame == 0)
					{
					anim_frame = 7
					
					var _hitbox = hitbox_create_melee(32, 4, 0.9, 0.6, 5, 7, 0.9, 8, 0, 2, SHAPE.circle, 1, FLIPPER.sakurai);
					_hitbox.hit_vfx_style = HIT_VFX.normal_strong;
					_hitbox.hit_sfx = snd_hit_strong;
					
					//Next phase
					attack_phase++;
					attack_frame = 25;
					}
				break;
				}
			//Endlag
			case 2:
				{
				//Animation
				if (attack_frame == 23)
					anim_frame = 8;
				if (attack_frame == 18)
					anim_frame = 9;	
				if (attack_frame == 13)
					anim_frame = 10;
				if (attack_frame == 8)
					anim_frame = 11;	
				if (attack_frame == 4)
					anim_frame = 12;	
					
				if (attack_frame == 0)
					{
					//Revert back to the original state
					attack_stop(PLAYER_STATE.aerial);
					run = false;
					}
				break;
				}
			}
		}
	//Movement
	move();
	
	//Hurtbox
	if (run)
		{
		hurtbox_anim_match(spr_joker_fair_hurtbox);
		}
	}
/* Copyright 2024 Springroll Games / Yosi */</pre>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Each attack is a separate script
      </p>
      <p>
        - Attack scripts can be organized in many ways, 
        but we recommend using the <code>attack_phase</code> and <code>attack_frame</code> format that all pre-made attacks use
      </p>
      <p>
        - Animations for attacks can be manually hardcoded or automatically run
      </p>
      <p>
        - Hitboxes are created through the <code>hitbox_create_*</code> functions, 
        and you can set addition hitbox variables using the dot operator
      </p>
      <p>
        - <router-link to="/docs/hurtbox_anim_match">hurtbox_anim_match</router-link>
        can be used to give players precise hurtboxes during attacks
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>