<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Explaining Character Palettes
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Palette Sprites</p>
      <p>
        Each character is assigned one <strong>Palette Sprite</strong>, which is a sprite containing all of the colors for every selectable palette.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb6_1.png" class="image"/>
      <br/>
      <p>
        The very first column of the palette sprite contains the <strong>"Sprite Colors"</strong>,
        which is all of the colors used in the character's animation files.
        You should try to include every color from the character's various sprites in this column (unless you want a color to stay the same on every palette).
      </p>
      <br/>
      <p>
        All of the other columns of the palette sprite contain the <strong>"In-Game Colors"</strong>.
        If you want to add or remove columns, resize the sprite.
      </p>
      <br/>
      <p class="title">Examples</p>
      <p>
        Using the default palette, you can see that the first palette in-game is not the same as the sprite:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb6_2.png" class="image"/>
      <br/>
      <p>
        If we changed the green in the first column to be a different color (red in this example), 
        then the green color of the sword will NOT be replaced in-game, because there are no red pixels in the original sprite:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb6_3.png" class="image"/>
      <br/>
      <img src="@/assets/Tutorials/tb6_4.png" class="image"/>
      <br/>
      <p>
        If we changed the green in the second column to be a different color (transparent white in this example), 
        then the white color in the sprite will be replaced with transparent white in-game:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb6_5.png" class="image"/>
      <br/>
      <img src="@/assets/Tutorials/tb6_6.png" class="image"/>
      <br/>
      <p class="title">VFX and Entities</p>
      <p>
        To create a visual effect that uses the player's palette colors, call the function
        <router-link to="/docs/vfx_create_color">vfx_create_color</router-link>.
        This creates an instance of <router-link to="/docs/obj_vfx_color">obj_vfx_color</router-link>,
        which is nearly identical to <router-link to="/docs/obj_vfx">obj_vfx</router-link>,
        except that it uses the color palette of its <code>owner</code> instance.
      </p>
      <br/>
      <p>
        Entity instances will automatically use the palette of the player that creates them.
        If you want to change the palette that an entity is currently using, you can set the 
        <code>palette_base</code>, <code>palette_swap</code>, <code>palette_data</code>, and <code>player_color</code> variables.
      </p>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Palette sprites are organized by columns, and determine what colors on sprites will be replaced in-game
      </p>
      <p>
        - All players and entities use palettes by default
      </p>
      <p>
        - You can create VFX that use palettes through the function <router-link to="/docs/vfx_create_color">vfx_create_color</router-link>
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>