<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to add a New Character
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Character Init Script</p>
      <p>
        Every character in PFE needs at least three things: 
        <ol>
          <li>
            A character init script
          </li>
          <li>
            A
            <router-link to="/docs/character_define">character_define</router-link> call in 
            <router-link to="/docs/character_data">character_data</router-link>
          </li>
          <li>
            A
            <router-link to="/docs/obj_css_zone">obj_css_zone</router-link> instance on the character select screen
          </li>
        </ol>
      </p>
      <br/>
      <p>
        A <strong>Character Init Script</strong> is a script that initializes all of the variables needed for a single character during gameplay.
        There are five character init scripts included in the project by default.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_1.png" class="image"/>
      <br/>
      <p>
        To create a new character init script, duplicate an existing character init script and change the name.
        For this tutorial, we'll duplicate the script <code>character_blocky_init</code>, rename it <code>character_tensor_init</code>, 
        and put it in a group on the asset browser called "Tensor" (for organization).
        Remember to also rename the function INSIDE the script:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_2.png" class="image"/>
      <br/>
      <p>
        And that's all there is to creating a character init script! Now obviously this new character is an exact clone of Blocky,
        but we'll come back later to change the variables.
      </p>
      <br/>

      <p class="title">Defining the Character</p>
      <p>
        The next step is to define the character in <router-link to="/docs/character_data">character_data</router-link>.
        Copy the character definition for Blocky, and paste it right before the character definition for Random.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_3.png" class="image"/>
      <br/>
      <p>
        Next, change the name of the character and the character init script they will use.
        For this tutorial, we'll name the character "Tensor" and assign it <code>character_tensor_init</code>.
        If you already have the render, portrait, stock sprite, victory theme, etc. you could add them in now.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_4.png" class="image"/>
      <br/>

      <p class="title">Adding the Character to the Character Select Screen</p>
      <p>
        The character select screen is a room named <code>rm_css</code> in <code>Menus > Character Select Screen</code>. (You can also use the Ctrl + T shortcut to search for it!)
        Once you have the room open, navigate to the "Character_Zones" layer.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_5.png" class="image"/>
      <br/>
      <p>
        Duplicate one of the <router-link to="/docs/obj_css_zone">obj_css_zone</router-link> instances, and resize the other ones to make space.
        For this tutorial, we'll take the easy route and just squash the Random button.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_6.png" class="image"/>
      <br/>
      <p>
        Double click on the new character zone instance, and then click on the "Variables" button to open the Variables tab.
        There should be only one variable in the tab: <code>character_name</code>. 
        If you're following along with the tutorial, the name is <code>Tensor</code>.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_7.png" class="image"/>
      <br/>
      <p>
        Now it's time to test the new character! Press F5 or click on the run button at the top.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_9.png" class="image"/>
      <br/>
      <p>
        The CSS should look something like this, with the name of the new character on the correct zone.
        If you select the character and start a match, it should play exactly like Blocky, but the win screen will have the new name.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_10.png" class="image"/>
      <br/>
      
      <p class="title">Changing Character Variables</p>
      <p>
        Now that we've got a new character set up, the next step is to change the values in the character init script.
        Most of the variables are self-explanatory.
      </p>
      <br/>
      <p>
        You might notice there are <code>if/else</code> chains for Airdodge values, Shield values, and Wall Jump values.
        The default scripts have this to ensure the characters will work no matter what
        <router-link to="/docs/airdodge_type">airdodge_type</router-link> / 
        <router-link to="/docs/shield_type">shield_type</router-link> / 
        <router-link to="/docs/wall_jump_type">wall_jump_type</router-link>
        the user chooses. If you already know what settings you want to use in your game, you can delete the code for the settings you won't use.
      </p>
      <br/>
      <p>
        Scroll down a bit further, and you'll get to the "Attacks" section.
        This stores all of the attack scripts that the character can call (by normal means).
      </p>
      <p>
        You can change the scripts here to <i>any</i> attack scripts.
        For example, you could change the neutral special ("Nspec") to <code>link_nspec_bow</code> and re-run the game, you'll be able to use the bow attack.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_11.png" class="image"/>
      <br/>
      <p>
        Note that there are NO parentheses after script names (<code>link_nspec_bow;</code>, not <code>link_nspec_bow();</code>).
        Putting parentheses after the name of a script will cause the script to run during initialization, which is not what we want in this context.
      </p>
      <br/>
      <p>
        You can also put the name of any attack defined in <router-link to="/docs/simple_attack_definitions">simple_attack_definitions</router-link>
        as a string to assign that attack to a character.
      </p>
      <br/>
      <p>
        Right after the "Attacks" section is the "Animations / Sprites" section.
        The <code>sprite_scale</code> variable determines the scale multiplier on all of the character's sprites.
        All of the default characters use a scale of 2, but if you want to use higher resolution sprites, you may want to use a scale of 1.
      </p>
      <br/>
      <p>
        The <code>my_sprites</code> struct stores the sprites the player uses for different states. The values of the <code>my_sprites</code> struct can be:
      </p>
      <ol>
        <li>The name of a sprite</li>
        <li><router-link to="/docs/anim_define">anim_define</router-link> / <router-link to="/docs/anim_define_ext">anim_define_ext</router-link></li>
        <li>-1 (no sprite)</li>
      </ol>
      <br/>
      <p>
        If the name of a sprite is given, then the character will use that sprite and automatically loop through the subimages.
      </p>
      <p>
        If you use 
        <router-link to="/docs/anim_define">anim_define</router-link> or <router-link to="/docs/anim_define_ext">anim_define_ext</router-link>,
        you get more control over how the sprite is drawn, and can optionally include another sprite or animation to play after the first is finished.
        Blocky's Entrance animation is an example of this - it plays the Entrance sprite, and then switches to the Idle sprite afterwards.
      </p>
      <br/>
      <p>
        Setting a sprite to -1 will cause the player to not change sprites. 
        This behavior can be seen in the double jump animation, which plays the "DJump_Rise" animation through the entire jump arc.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb1_12.png" class="image"/>
      <br/>
      <p>
        -1 is also used for the Ledge Tether and Wall Cling animations in Blocky's character init script as a placeholder, since Blocky cannot tether to ledges or wall cling.
      </p>
      <br/>
      <p class="title">Custom Scripts</p>
      <p>
        The "Properties" section of the character init script has a place for custom scripts.
        The draw script is a script that replaces <router-link to="/docs/player_draw_self">player_draw_self</router-link> for the character.
      </p>
      <br/>
      <p>
        There are multiple "callbacks" that you can add any number of scripts to by using <router-link to="/docs/callback_add">callback_add</router-link>:
      </p>
      <ul>
        <li><code>callback_passive</code> - Scripts to be run every frame, regardless of what state the player is in</li>
        <li><code>callback_hud</code> - Allows characters to draw HUD elements</li>
        <li><code>callback_overhead</code> - Allows characters to draw overhead elements</li>
        <li><code>callback_draw_begin</code> - Allows characters to draw sprites before their animation sprite is drawn</li>
        <li><code>callback_draw_end</code> - Allows characters to draw sprites after their animation sprite is drawn</li>
        <li><code>callback_hit</code> - Runs whenever the player hits something</li>
        <li><code>callback_hurt</code> - Runs whenever the player is hurt by something</li>
      </ul>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Every character needs a Character Init Script, 
        <router-link to="/docs/character_define">character_define</router-link> call,
        and a spot on the Character Select Screen
      </p>
      <p>
        - Character Init Scripts set all of the in-game properties of a character
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>