<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Explaining Player States
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">State Scripts</p>
      <p>
        Every instance of <router-link to="/docs/obj_player">obj_player</router-link> in PFE 
        uses a simple state machine. 
        Players can only be in <i>one</i> state at a time, which can be changed using
        <router-link to="/docs/state_set">state_set</router-link>.
      </p>
      <br/>
      <p>
        Each state is a separate script (which is quite similar to how attack scripts work).
        State scripts can have three phases:
      </p>
      <ol>
        <li><code>PLAYER_STATE_PHASE.start</code>:
          A phase that is run immediately when the state is set via <router-link to="/docs/state_set">state_set</router-link>
        </li>
        <li><code>PLAYER_STATE_PHASE.normal</code>:
         The code run by the state every frame.
         Please note: If <router-link to="/docs/state_set">state_set</router-link> is called while the current state script is
         running, or after it has already run for the frame, the newly set state will NOT run the normal phase until the next
         frame
        </li>
        <li><code>PLAYER_STATE_PHASE.stop</code>:
          A phase that is run immediately when the state is switched off via <router-link to="/docs/state_set">state_set</router-link>
        </li>
      </ol>
      <br/>
      <p>
        By default, all characters use the "Standard" state scripts. 
        However, this can be changed per-character by assigning different scripts to the <code>my_states</code> array in the character's init script.
      </p>
      <br/>
      <p class="title">List of States</p>
      <ol>
        <li>
          <router-link to="/docs/standard_idle">idle</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_crouching">crouching</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_walking">walking</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_walk_turnaround">walk_turnaround</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_dashing">dashing</router-link>
          (For when players initially begin to run)
        </li>
        <li>
          <router-link to="/docs/standard_running">running</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_run_turnaround">run_turnaround</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_run_stop">run_stop</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_jumpsquat">jumpsquat</router-link>
          (When a player starts to jump, but hasn't left the ground yet)
        </li>
        <li>
          <router-link to="/docs/standard_aerial">aerial</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_airdodging">airdodging</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_wavelanding">wavelanding</router-link>
          (Only used if the <router-link to="/docs/airdodge_type">airdodge_type</router-link> is set to 
          <code>AIRDODGE_TYPE.momentum_stop</code>)
        </li>
        <li>
          <router-link to="/docs/standard_rolling">rolling</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_shielding">shielding</router-link>
          (The specifics of this state will differ based on the <router-link to="/docs/shield_type">shield_type</router-link>)
        </li>
        <li>
          <router-link to="/docs/standard_shield_release">shield_release</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_shield_break">shield_break</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_parry_press">parry_press</router-link>
          (Only used if the <router-link to="/docs/shield_type">shield_type</router-link> is set to 
          <code>SHIELD_TYPE.parry_press</code>)
        </li>
        <li>
          <router-link to="/docs/standard_parry_stun">parry_stun</router-link>
          (Only used if the <router-link to="/docs/shield_type">shield_type</router-link> is set to 
          <code>SHIELD_TYPE.parry_press</code>)
        </li>
        <li>
          <router-link to="/docs/standard_spot_dodging">spot_dodging</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_hitlag">hitlag</router-link>
          (When a player is in "freeze frames" due to getting hit by an attack.
          Please note: The attacking player does NOT use the hitlag state for "freeze frames". 
          Instead, they keep the same state and set the <code>self_hitlag_frame</code>)
        </li>
        <li>
          <router-link to="/docs/standard_hitstun">hitstun</router-link>
          (When a player is being launched by an attack)
        </li>
        <li>
          <router-link to="/docs/standard_tumble">tumble</router-link>
          (After a player's hitstun ends, if the <code>is_reeling</code> variable is true, they will enter the tumble state until they take an action)
        </li>
        <li>
          <router-link to="/docs/standard_helpless">helpless</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_magnetized">magnetized</router-link>
          (When a player is magnetized by a <router-link to="/docs/obj_hitbox_magnetbox">magnetbox</router-link>)
        </li>
        <li>
          <router-link to="/docs/standard_flinch">flinch</router-link>
          (Used by some attacks to deal weak damage and knockback without launching the opponent)
        </li>
        <li>
          <router-link to="/docs/standard_balloon">balloon</router-link>
          (An alternate version of the "hitstun" state where the player moves faster at the start, and then quickly slows down)
        </li>
        <li>
          <router-link to="/docs/standard_landing_lag">landing_lag</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_knockdown">knockdown</router-link>
          (When a player is launched onto the ground and doesn't tech or bounce)
        </li>
        <li>
          <router-link to="/docs/standard_getup">getup</router-link>
          (When a player is getting up from being knocked down)
        </li>
        <li>
          <router-link to="/docs/standard_tech_rolling">tech_rolling</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_teching">teching</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_tech_wall_jump">tech_wall_jump</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_snap">ledge_snap</router-link>
          (When a player is initially grabbing the ledge. During this state, players do NOT have invincibility)
        </li>
        <li>
          <router-link to="/docs/standard_ledge_hang">ledge_hang</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_getup">ledge_getup</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_jump">ledge_jump</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_roll">ledge_roll</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_attack">ledge_attack</router-link>
          (When a player gets up from the ledge to do a ledge attack. The actual attack uses the <code>attacking</code> state)
        </li>
        <li>
          <router-link to="/docs/standard_ledge_tether">ledge_tether</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_ledge_trump">ledge_trump</router-link>
          (Only used if the <router-link to="/docs/ledge_type">ledge_type</router-link> is set to 
          <code>LEDGE_TYPE.trump</code>)
        </li>
        <li>
          <router-link to="/docs/standard_wall_cling">wall_cling</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_wall_jump">wall_jump</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_knocked_out">knocked_out</router-link>
          (When a player has been knocked out and is waiting to respawn)
        </li>
        <li>
          <router-link to="/docs/standard_star_ko">star_ko</router-link>
          (When a player is in the Star KO animation)
        </li>
        <li>
          <router-link to="/docs/standard_screen_ko">screen_ko</router-link>
          (When a player is in the Screen KO animation)
        </li>
        <li>
          <router-link to="/docs/standard_respawning">respawning</router-link>
          (When a player is on the respawn platform)
        </li>
        <li>
          <router-link to="/docs/standard_attacking">attacking</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_grabbing">grabbing</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_grabbed">grabbed</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_grab_release">grab_release</router-link>
        </li>
        <li>
          <router-link to="/docs/standard_lost">lost</router-link>
          (When a player has no stocks left in a match and is permanently out)
        </li>
        <li>
          <router-link to="/docs/standard_entrance">entrance</router-link>
          (Used for the entrance animations of characters at the start of a match)
        </li>
      </ol>
      <br/>
      <p class="title">Debugging</p>
      <p>
        When <router-link to="/tutorialI1">debug mode</router-link> is turned on,
        you can press <code>F4</code> to display overhead debug data.
        This includes a list of the player's last 5 states, which can be useful for debugging scenarios where a player
        changes state multiple times within a single frame.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb5_1.png" class="image"/>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Each player instance can be in one state at a time
      </p>
      <p>
        - The actions a player can perform in a state are determined by the state script assigned to the character
      </p>
      <p>
        - By default, all characters use the "Standard" state scripts
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>