import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import FAQ from '@/views/FAQ.vue'
import Setup from '@/views/Setup.vue'
import Documentation from '@/views/Documentation.vue'
import Doc from '@/components/Doc.vue'
import Tutorials from '@/views/Tutorials.vue'
import Media from '@/views/Media.vue'
import About from '@/views/About.vue'
import Feature_List from '@/views/Feature_List.vue'

//Tutorials
import TutorialP1 from '@/views/tutorials/TutorialP1.vue'
import TutorialP2 from '@/views/tutorials/TutorialP2.vue'
import TutorialB1 from '@/views/tutorials/TutorialB1.vue'
import TutorialB2 from '@/views/tutorials/TutorialB2.vue'
import TutorialB3 from '@/views/tutorials/TutorialB3.vue'
import TutorialB4 from '@/views/tutorials/TutorialB4.vue'
import TutorialB5 from '@/views/tutorials/TutorialB5.vue'
import TutorialB6 from '@/views/tutorials/TutorialB6.vue'
import TutorialB7 from '@/views/tutorials/TutorialB7.vue'
import TutorialB8 from '@/views/tutorials/TutorialB8.vue'

import TutorialI1 from '@/views/tutorials/TutorialI1.vue'
import TutorialI2 from '@/views/tutorials/TutorialI2.vue'
import TutorialI3 from '@/views/tutorials/TutorialI3.vue'
import TutorialI4 from '@/views/tutorials/TutorialI4.vue'
import TutorialI5 from '@/views/tutorials/TutorialI5.vue'

import TutorialA1 from '@/views/tutorials/TutorialA1.vue'
import TutorialA2 from '@/views/tutorials/TutorialA2.vue'

const routes = [
	{
		path: '/',
		name: 'Platform Fighter Engine',
		component: Home
	},
	{
		path: '/faq',
		name: 'FAQ',
		component: FAQ
	},
	{
		path: '/setup',
		name: 'Setup',
		component: Setup
	},
	{
		path: '/docs',
		name: 'Documentation',
		component: Documentation,
		children: [
			{
				path: ':name',
				component: Doc
			},
		]
	},
	{
		path: '/tutorials',
		name: 'Tutorials',
		component: Tutorials
	},
	{
		path: '/media',
		name: 'Media',
		component: Media
	},
	{
		path: '/about',
		name: 'About',
		component: About
	},
	{
		path: '/feature_list',
		name: 'Feature List',
		component: Feature_List
	},

	//Tutorials
	{
		path: '/tutorialP1',
		name: "Tutorial P1",
		component: TutorialP1,
	},
	{
		path: '/tutorialP2',
		name: "Tutorial P2",
		component: TutorialP2,
	},
	{
		path: '/tutorialB1',
		name: "Tutorial B1",
		component: TutorialB1,
	},
	{
		path: '/tutorialB2',
		name: "Tutorial B2",
		component: TutorialB2,
	},
	{
		path: '/tutorialB3',
		name: "Tutorial B3",
		component: TutorialB3,
	},
	{
		path: '/tutorialB4',
		name: "Tutorial B4",
		component: TutorialB4,
	},
	{
		path: '/tutorialB5',
		name: "Tutorial B5",
		component: TutorialB5,
	},
	{
		path: '/tutorialB6',
		name: "Tutorial B6",
		component: TutorialB6,
	},	
	{
		path: '/tutorialB7',
		name: "Tutorial B7",
		component: TutorialB7,
	},
	{
		path: '/tutorialB8',
		name: "Tutorial B8",
		component: TutorialB8,
	},

	{
		path: '/tutorialI1',
		name: "Tutorial I1",
		component: TutorialI1,
	},
	{
		path: '/tutorialI2',
		name: "Tutorial I2",
		component: TutorialI2,
	},
	{
		path: '/tutorialI3',
		name: "Tutorial I3",
		component: TutorialI3,
	},
	{
		path: '/tutorialI4',
		name: "Tutorial I4",
		component: TutorialI4,
	},
	{
		path: '/tutorialI5',
		name: "Tutorial I5",
		component: TutorialI5,
	},

	{
		path: '/tutorialA1',
		name: "Tutorial A1",
		component: TutorialA1,
	},
	{
		path: '/tutorialA2',
		name: "Tutorial A2",
		component: TutorialA2,
	},
]

const router = createRouter({
	history: createWebHashHistory(),
	scrollBehavior(to) {
		//Always scroll to the top, except for on the docs
		if (!to.fullPath.includes("docs")) {
			return { top : 0 }
		}
	},
	routes,
})

export default router