<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Explaining Passive Scripts
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">What's a "Passive Script"?</p>
      <p>
        Most of the code in PFE is inside attack scripts and state scripts.
        However, there are times when you want code to be run outside of attacks, and independent of the player's state.
        This is where <strong>Passive Scripts</strong> come in.
      </p>
      <br/>
      <p>
        "Passive scripts" are any scripts added to the player's <code>callback_passive</code> variable through the function <router-link to="/docs/callback_add">callback_add</router-link>.
        This is typically done in the "Custom Scripts" section of a character's init script.
        Every frame, after the state script and attack script for a player are run, all of the scripts stored in <code>callback_passive</code> are run, one by one.
        You can remove passive scripts at any time using <router-link to="/docs/callback_remove">callback_remove</router-link>.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb8_1.png" class="image"/>
      <br/>
      <p>
        Because passive scripts are run EVERY frame, they have many use cases, including:
      </p>
      <ul>
        <li>- Timers</li>
        <li>- Meter management</li>
        <li>- Buffs and Debuffs</li>
        <li>- Motion Input moves</li>
        <li>- Handling move cooldowns and limited uses</li>
        <li>- Character-specific mechanics</li>
      </ul>
      <br/>
      <p>
        To make a new passive script, you can copy an existing passive script that is close to what you want or start from scratch with the <code>passive_script_template</code> script.
      </p>
      <br/>
      <p class="title">The Custom Passive Struct</p>
      <p>
        The player object has a variable called <code>custom_passive_struct</code>, which is a struct that stores all variables used by passive scripts.
        You can use the dot accessor to easily modify variables in the struct.
        For example, if you wanted to make a variable to store how many charges a character has left for an ability, you could initialize it like this...
      </p>
      <pre>//Initialize variable
if (!variable_struct_exists(custom_passive_struct, "ability_charges"))
	{
	custom_passive_struct.ability_charges = 5;
	}</pre>
      <p>
        ...and then use <code>custom_passive_struct.ability_charges</code> to reference the variable everywhere else in the script.
      </p>
      <br/>
      <p>
        Remember to <strong>ALWAYS</strong> use the <code>custom_passive_struct</code> for new variables!
        Forgetting to put a variable in the struct will result in a <strong>"Variable not set before reading"</strong> crash when playing online.
      </p>
      <br/>
      <p class="title">Example Passive Scripts</p>
      <p>
        There are plenty of example passive scripts in the engine that you can copy from or modify to create the mechanics you want.
        Here's a list of the most useful ones:
      </p>
      <br/>
      <ul>
        <li>
          <code>ex_meter_passive</code> - This is the script given to players when <router-link to="/docs/match_ex_meter">setting().match_ex_meter</router-link> is turned on.
          It slowly charges the player's EX meter variables during gameplay, and subtracts from the player's meter when they get knocked out.
        </li>
        <li>
          <code>limit_gauge_passive</code> - This script manages a "Limit Gauge" for the player, and allows them to use a different Forward Special when the gauge is filled.
        </li>
        <li>
          <code>poison_passive</code> - Damages the player every half second until the timer runs out.
        </li>
        <li>
          <code>spark_cancel_passive</code> - Gives the player the ability to do "Spark Cancels" (inspired by the mechanics of Rushdown Revolt).
        </li>
        <li>
          <code>auto_turnaround_passive</code> - Makes the player turn to face the nearest opponent in specific states.
        </li>
        <li>
          <code>airdash_passive</code> - Allows the player to perform a psuedo-airdash while in the air by pressing Grab.
        </li>
        <li>
          <code>tension_gauge_passive</code> - Gives the player a meter that increases and decreases depending on what actions they take.
          This meter can be spent to cancel attacks early by pressing Taunt.
        </li>
        <li>
          <code>command_inputs_passive</code> - Allows the player to use motion inputs to perform special attacks.
        </li>
        <li>
          <code>shulk_nspec_monado_arts_passive</code> - Works with the <code>shulk_nspec_monado_arts</code> attack script to change the player's stats based on what Art they have selected.
        </li>
        <li>
          <code>bayonetta_fspec_afterburner_passive</code> - Limits the player to 2 uses of Afterburner Kick per airtime.
        </li>
      </ul>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Passive scripts can be used for any code that you want to run EVERY single frame
      </p>
      <p>
        - Use <router-link to="/docs/callback_add">callback_add</router-link> in a character's init script to give that character a permanent passive script
      </p>
      <p>
        - Use <router-link to="/docs/callback_remove">callback_remove</router-link> at any time to remove a passive script
      </p>
      <p>
        - There any many example passive scripts you can use as a base
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>