<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          Rollback Netcode Overview
        </p>
        <div>Last updated for Version 1.4.1</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">What is Rollback?</p>
      <p>
        "Rollback" is a style of netcode that allows the game to "predict" the opponent's inputs before they are received over the network.
        When the actual inputs from the opponent arrive, the game "rolls back" to the frame before the prediction, and inserts the correct inputs.
        Because the game does not need to wait for the opponent's inputs, it will not have to freeze immediately if the opponent's inputs take longer than normal to be transmitted.
        However, if the game's predicted inputs do not match the correct inputs, the game may display incorrect frames and show players "teleporting" to the correct positions later on.
      </p>
      <br/>
      <p>
        <strong>Essentially, rollback allows players to use lower input delays by sacrificing the accuracy of what is displayed to the players.</strong>
      </p>
      <br/>
      <p>
        On the technical side of things, rollback requires the game to be able to save and load the <i>entire</i> game state,
        as well as run multiple logic frames in the span of one rendering frame (~16ms if the game is running at 60fps).
        This means games must be sufficiently optimized to run rollback, or have above-average required specs.
      </p>
      <br/>
      <p>
        If you want to read more about rollback or just netcode in general, here are some good articles:
      </p>
      <ul>
        <li>- <a href="https://bymuno.com/post/rollback" target="_blank" rel="noopener">What is Rollback Netcode?</a> by Muno</li>
        <li>- <a href="https://yal.cc/preparing-your-game-for-deterministic-netcode/" target="_blank" rel="noopener">Preparing your game for deterministic netcode</a> by Yellow Afterlife</li>
        <li>- <a href="https://blog.hypersect.com/rollback-networking-in-inversus/" target="_blank" rel="noopener">Rollback Networking in Inversus</a> by Hypersect</li>
        <li>- <a href="https://meseta.medium.com/netcode-concepts-part-3-lockstep-and-rollback-f70e9297271" target="_blank" rel="noopener">Netcode Concepts Part 3: Lockstep and Rollback</a> by Meseta</li>
        <li>- <a href="https://antsstyle.medium.com/netcode-in-games-an-explanation-and-why-rollback-is-overrated-b76ee54ac2bb" target="_blank" rel="noopener">Explaining netcode in games, and why rollback is overrated (though still worth adopting)</a> by Antsstyle</li>
        <li>- <a href="https://www.snapnet.dev/blog/netcode-architectures-part-2-rollback/" target="_blank" rel="noopener">Netcode Architectures Part 2: Rollback</a> by SnapNet</li>
        <li>- <a href="https://github.com/ThusSpokeNomad/GameNetworkingResources#readme" target="_blank" rel="noopener">Game Networking Resources</a> by ThusSpokeNomad</li>
        <li>- <a href="https://gist.github.com/rcmagic/f8d76bca32b5609e85ab156db38387e9" target="_blank" rel="noopener">Rollback Pseudocode</a> by Zinac</li>
      </ul>
      <br/>
      <p class="title">The "ggmr_settings" Script</p>
      <p>
        "Good GameMaker Rollback", or GGMR, is the netcode framework used by PFE.
        You can download it separately from itch.io <a href="https://springrollgames.itch.io/ggmr" target="_blank" rel="noopener">here</a>.
      </p>
      <br/>
      <p>
        All of the constants for GGMR are defined in the <router-link to="/docs/ggmr_settings">ggmr_settings</router-link> script:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta2_1.png" class="image"/>
      <br/>
      <p>
        Nost of these settings are explained fully through the comments / documentation, but there are a few that require more thorough explanations:
      </p>
      <br/>
      <ul>
        <li><code>GGMR_PLAYERS_MAX</code>: As stated in the comment, this is the maximum number of players that can be local or remote in a single match.
        However, PFE online only supports 1 local player, so all of the other players must be remote.</li>
        <br/>
        <li><code>GGMR_MEMBERS_MAX / GGMR_CLIENTS_MAX</code>: These numbers override the maximum number of players and spectators.
        For example, the if you allow 4 players and 6 spectators, but only 8 members, then the lobby limit is 8 people rather than 10 people.</li>
        <br/>
        <li><code>GGMR_PREDICTION_FRAMES_MAX</code>: How many frames the game can "predict" (repeat) remote inputs before having to stall the game and wait for the remote inputs to be received.
        Once the correct inputs are received, the game needs to roll back to the last state before any predictions happened, and run the game with the correct inputs up until the current frame.
        This means that increasing the number of prediction frames will put more stress on the game on long-distance connections, because more frames will need to be executed to "catch up".
        As such, this number should be kept low to ensure that the game runs at 60fps on more hardware.</li>
        <br/>
        <li><code>GGMR_INPUT_DELAY_MIN</code>: This number is negative by default to allow the "negative input delay" option for local games, but the online lobby UI doesn't allow players to use a negative input delay in online games.
        Additionally, the UI for online Quickplay doesn't allow players to choose an input delay below <code>GGMR_INPUT_DELAY_DEFAULT</code>.</li>
        <br/>
        <li><code>GGMR_PORT</code>: This is only the DEFAULT port used - you can change the port using the function <router-link to="/docs/ggmr_net_port_change">ggmr_net_port_change</router-link>.</li>
        <br/>
        <li><code>GGMR_RIFT_SLOW_THRESHOLD</code>: Because the rift between players is calculated as the average of multiple values, it can be a non-integer number.
        Once the rift is large enough, the game will "slow down" by dropping 1 frame every <code>GGMR_RIFT_SLOW_INTERVAL</code> number of frames.
        The <code>GGMR_RIFT_ACCEL_THRESHOLD</code> and <code>GGMR_RIFT_ACCEL_INTERVAL</code> macros work the same way, but involve the game running 2 frames instead of dropping frames.</li>
        <br/>
        <li><code>GGMR_SESSION_FRAME_SKIP_ENABLED</code>: Allows the game to run faster if it is running slower than 60fps.
        Unlike the rift macros, this does not take remote players into account.</li>
      </ul>
      <br/>
      <p class="title">Player Terminology</p>
      <p>
        There's a lot of terminology used throughout the GGMR systems to refer to players, so understanding it is crucial for making sure menus pass along data correctly.
      </p>
      <br/>
      <p>
        While in the lobby, all players who connect to the lobby are "members".
        You can use functions such as <router-link to="/docs/ggmr_lobby_member_set">ggmr_lobby_member_set</router-link> to manage lobby members.
        The "client_type" of the member determines if they will be a spectator or a player once the game starts.
      </p>
      <br/>
      <p>
        When the game starts, all lobby members are converted to "clients".
        The "client_type" variable is still used to separate spectators from players, since they are ultimately stored in the same list.
        GGMR does not support adding or removing clients during gameplay, so you'd have to end the session and create a new session if you wanted to do that.
      </p>
      <br/>
      <p class="title">What Scripts Are Used?</p>
      <p>
        The GGMR system for gameplay sessions is set up in <router-link to="/docs/obj_game_online">obj_game_online</router-link>.
        You can see what scripts it assigns for each part of the system here:
      </p>
      <br/>
      <img src="@/assets/Tutorials/ta2_2.png" class="image"/>
      <br/>
      <p>
        As with local matches, all game logic must happen in <router-link to="/docs/game_advance_frame">game_advance_frame</router-link>, or else the netcode will be at risk of desyncing.
      </p>
      <br/>
      <p class="title">Saving and Loading Variables</p>
      <p>
        At the core of rollback are the scripts that save and load the entire game state -
        <router-link to="/docs/game_state_save">game_state_save</router-link> and <router-link to="/docs/game_state_load">game_state_load</router-link>.
        The save script is organized as follows:
      </p>
      <ol>
        <li>
          <router-link to="/docs/game_state_save_object">game_state_save_object</router-link> is called for each gameplay object, with the corresponding <code>__optimized</code> script.
          There can be any number of these gameplay objects in the room, so an "end flag" needs to be written in the buffer to signify the end of this section.
        </li>
        <li>
          The instance id struct is saved as JSON and written to the buffer.
          This struct ensures that instance id references do not break when the game state is loaded.
        </li>
        <li>
          Variables for <router-link to="/docs/obj_game">obj_game</router-link>, <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>, and
          <router-link to="/docs/obj_verlet_system">obj_verlet_system</router-link> are saved.
          There should only be 1 of each of these objects in the room.
        </li>
      </ol>
      <br/>
      <p>
        The load script works in the same order:
      </p>
      <ol>
        <li>
          All gameplay objects in the room currently are destroyed.
        </li>
        <li>
          <router-link to="/docs/game_state_load_objects_spawn">game_state_load_objects_spawn</router-link> is called to spawn in every instance, without setting any variables yet.
          Every instance MUST be created before the variables are set, because otherwise we'd run into issues trying to get the instance ids of instances that are not created yet.
        </li>
        <li>
          The newly created instances are looped through again, and the <code>__optimized</code> scripts are called for each.
        </li>
        <li>
          Variables for <router-link to="/docs/obj_game">obj_game</router-link>, <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>, and
          <router-link to="/docs/obj_verlet_system">obj_verlet_system</router-link> are loaded.
        </li>
        <li>
          Special cases are handled - this includes running the character init script to set up variables marked as "static", registering sync ids for the new instances, and correcting instance ids to their new values.
        </li>
      </ol>
      <br/>
      <p>
        All of the <code>__optimized</code> scripts work by writing and reading instances variables to the buffer.
        <strong>The order and data types of the variables MUST match between the pairs of save and load scripts</strong>.
        So if in one script you save the variables in the order <code>damage</code> -> <code>stamina</code> -> <code>stocks</code>,
        the corresponding load script must also be in the order <code>damage</code> -> <code>stamina</code> -> <code>stocks</code>.
      </p>
      <br/>
      <p>
        The save and load scripts for <router-link to="/docs/obj_player">obj_player</router-link> are slightly different than normal.
        The section at the top covers all variables that are set up in the Character Init Scripts.
        The static macros, such as <router-link to="/docs/character_static_states">character_static_states</router-link>,
        are checked in this section to determine if certain variables should be saved and loaded or not.
        Variables from <router-link to="/docs/player_init_start">player_init_start</router-link> are handled next, and then the built-in variables are at the very end.
      </p>
      <br/>
      <p>
        <strong>If you want to add ANY new variables to gameplay objects in PFE, those variables MUST be added to the corresponding save and load scripts, in matching positions.</strong>
        Variables contained within structs, such as the <code>custom_passive_struct</code> or <code>custom_entity_struct</code>,
        do NOT need to be added to the save and load scripts, since the scripts already handle those structs.
      </p>
      <br/>
      <p class="title">Desyncs</p>
      <p>
        A "desync" is when one player's game does not match the other player's game.
        PFE has very simple desync detection system, using the <router-link to="/docs/game_state_hash">game_state_hash</router-link> script.
        This checks the position, state, and damage of each player object, as well as the position and type of each hitbox.
        During online matches, this check is performed every <router-link to="/docs/GGMR_SESSION_DESYNC_TEST_INTERVAL">GGMR_SESSION_DESYNC_TEST_INTERVAL</router-link> number of frames.
        If a desync is detected, the game immediately ends.
      </p>
      <br/>
      <p>
        The default system does not comprehensively check for desyncs.
        For example, it does not monitor variables such as the player's EX meter.
        As such, you may want to change what variables are incorporated in the hash for better testing.
      </p>
      <br/>
      <p>
        The <router-link to="/docs/debug_sync_test">setting().debug_sync_test</router-link> variable toggles local rollback testing.
        This will cause the game to save, load, and check for desyncs every single frame.
        <strong>You should ALWAYS test new features with debug sync test turned on, because the sync test checks for desyncs every frame, while online games only periodically check for desyncs.</strong>
      </p>
      <br/>
      <p>
        There are a lot of things that can cause desyncs.
        Here is a list of the most common ones:
      </p>
      <ol>
        <li>
          <strong>Running game logic outside of <router-link to="/docs/game_advance_frame">game_advance_frame</router-link></strong>:
          Any code that is not in an event or script called from <router-link to="/docs/game_advance_frame">game_advance_frame</router-link>
          will cause a desync.
        </li>
        <li>
          <strong>Relying on time</strong>:
          Anything that checks the computer clock, uses a GameMaker Alarm, or uses a GameMaker Time Source, will cause a desync.
        </li>
        <li>
          <strong>Code in the Draw Event</strong>:
          If any code in Draw Events affects gameplay, the game will desync.
        </li>
        <li>
          <strong>Relying on instance order</strong>:
          Using <code>with</code>, <code>instance_find</code>, etc. may cause desyncs, if the code being run requires the order to be the same every time.
          The function <router-link to="/docs/with_synced_object">with_synced_object</router-link> can be used as a way to loop through gameplay instances in a guaranteed order.
        </li>
        <li>
          <strong>Random numbers</strong>:
          Using any random functions in code that affects gameplay will cause desyncs.
          The PRNG functions can be used instead; for example, <router-link to="/docs/prng_choose">prng_choose</router-link> can be used in place of the normal <code>choose</code> function.
        </li>
      </ol>
      <br/>
      <p class="title">Summary</p>
      <p>
        - Rollback allows players to use lower input delays by sacrificing the accuracy of what is displayed
      </p>
      <p>
        - The GGMR system's settings are in the <router-link to="/docs/ggmr_settings">ggmr_settings</router-link> script
      </p>
      <p>
        - In lobbies, players are called "members" and are designated to be spectators or players based on their "client_type"
      </p>
      <p>
        - In gameplay, players are called "clients" and the "client_type" property is still used
      </p>
      <p>
        - <router-link to="/docs/obj_game_online">obj_game_online</router-link> sets up the GGMR session for online matches
      </p>
      <p>
        - The <router-link to="/docs/game_state_save">game_state_save</router-link> and <router-link to="/docs/game_state_load">game_state_load</router-link>
        scripts handle saving and loading the entire game
      </p>
      <p>
        - If you want to add ANY new variables to gameplay objects in PFE, those variables MUST be added to the corresponding save and load scripts, in matching positions
      </p>
      <p>
        - Desyncs are detected using the <router-link to="/docs/game_state_hash">game_state_hash</router-link> script
      </p>
      <p>
        - You should ALWAYS test new features with <router-link to="/docs/debug_sync_test">setting().debug_sync_test</router-link> turned on
      </p>
      <p>
        - There are a variety of things that commonly cause desyncs in games
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>