<template>
	<div class="container">
		<section class="hero is-primary">
			<div class="hero-body">
				<p class="title">
					Controlling the CPU
				</p>
				<div>Last updated for Version 1.4.1</div>
			</div>
		</section>
		<section class="section main-part">
			<p class="title">The CPU Script Base</p>
			<p>
				CPUs are controlled through the same input system players use, so that they are compatible with replays.
				The inputs a CPU uses each frame are determined by the <router-link to="/docs/cpu_script_base">cpu_script_base</router-link> script.
			</p>
			<br/>
			<img src="@/assets/Tutorials/ti3_1.png" class="image"/>
			<br/>
			<p>
				The first important section of the <router-link to="/docs/cpu_script_base">cpu_script_base</router-link> script
				is "CPU Stage Data", which gets values from <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link> to help the CPU recover.
				The <code>_cpu_up_b_distance</code> is the distance from the bottom of the room that default CPUs will attempt to use Up Special from.
				The <code>_cpu_main_stage_distance</code> is the distance from the center of the room that default CPUs need to be before trying to move back to the center.
			</p>
			<br/>
			<p>
				The next section is "CPU Types", which determines the inputs for CPUs based on which type was chosen for them on the character select.
				You can change what types are available by modifying the <code>CPU_TYPE</code> enum and adding more cases to the switch statement.
			</p>
			<br/>
			<p class="title">Character-specific CPU Scripts</p>
			<p>
				You can set character-specific scripts for CPUs by assigning the script to a character in <router-link to="/docs/character_data">character_data</router-link>.
				These scripts should be based on the template - <router-link to="/docs/cpu_script_template">cpu_script_template</router-link>.
				Since the character-specific scripts are run after the default CPU behavior, you can choose to override any inputs or stick values as needed.
			</p>
			<br/>
			<p class="title">Input Functions</p>
			<p>
				The CPU has three special functions for making inputs - 
				<router-link to="/docs/cpu_press">cpu_press</router-link>,
				<router-link to="/docs/cpu_hold">cpu_hold</router-link>, and
				<router-link to="/docs/cpu_release">cpu_release</router-link>.
				These are pretty self-explanatory - they take an input from the <code>INPUT</code> enum, and either press, hold, or release that input on the current frame.
				Please note that normal input buffering still occurs for CPUs, so by default pressed inputs will last for 6 frames
				(unless you change <router-link to="/docs/buffer_time_standard">buffer_time_standard</router-link>).
			</p>
			<br/>
			<p class="title">Moving the Control Stick</p>
			<p>
				The local variables <code>_lx</code>, <code>_ly</code>, <code>_rx</code>, and <code>_ry</code> 
				can be used to control the current positions of the CPU's control sticks.
				This works the exact same way a player's control stick does, so if you want a CPU to flick the stick in a direction, they'll need to set it to neutral for a frame
				and then set it in the desired direction.
				Control stick values must always be from <code>-1</code> to <code>1</code>.
			</p>
			<br/>
			<p class="title">Examples</p>
			<strong>Starting an UP Special:</strong>
			<pre>_ly = -1;
cpu_press(INPUT.special);</pre>
			<br/>
			<strong>Moving horizontally towards the nearest player:</strong>
			<pre>var _nearest = find_nearest_player(x, y, infinity, player_team);
if (_nearest != noone)
	{
	if (_nearest.x > x)
		_lx = 1;
	else
		_lx = -1;
	}</pre>
			<br/>
			<strong>Using a random attack in a random direction, with a small chance to grab:</strong>
			<pre>_lx = random_range(-1, 1);
_ly = random_range(-1, 1);
cpu_press(choose_weighted([INPUT.attack, 25, INPUT.grab, 5]));</pre>
			<br/>
			<strong>Dropping through platforms:</strong>
			<pre>if (on_plat())
	{
	_ly = 1;
	}</pre>
			<br/>
			<strong>Automatically pummel opponents and throw them offstage:</strong>
			<pre>if (state == PLAYER_STATE.grabbing)
	{
	//Pummel before the opponent breaks free
	if (state_frame > 30)
		{
		cpu_press(INPUT.attack);
		}
	//Throw the opponent offstage
	else
		{
		_lx = -sign((room_width / 2) - x);
		}
	}</pre>
			<br/>
			<p class="title">Summary</p>
			<p>
				- The <router-link to="/docs/cpu_script_base">cpu_script_base</router-link> script determines the inputs of the CPU during a match
			</p>
			<p>
				- You can use <router-link to="/docs/cpu_press">cpu_press</router-link>,
				<router-link to="/docs/cpu_hold">cpu_hold</router-link>, and
				<router-link to="/docs/cpu_release">cpu_release</router-link>
				to make the CPU perform specific inputs
			</p>
			<p>
				- The local variables <code>_lx</code>, <code>_ly</code>, <code>_rx</code>, and <code>_ry</code> 
				can be used to control the current position of the CPU's control sticks
			</p>
			<br/>
			<button class="button">
				<router-link to="/tutorials">
					Back to Tutorials
				</router-link>
			</button>
		</section>
	</div>
</template>