<template>
  <div>
    <div class="title">List of all Objects</div>
    <input class="input block" type="text" placeholder="Filter..." @change="filter_all($event.target.value)"/>
    <button class="button block mr-3" @click="toggle_all()"><strong>Toggle All</strong></button>
    <button class="button block" @click="show_descriptions ^= true"><strong>Show Descriptions</strong></button>
    <br/>
    <div v-for="category in sorted_categories" :key="category">
      <div class="box" v-show="any_strings_contain(object_categories[category], filter_value)">
        <strong style="cursor: pointer;" @click="show_related_objects[category] ^= true">{{ category }}</strong>
        <div v-show="show_related_objects[category]" v-for="obj in object_categories[category]" :key="obj">
          <div v-show="string_contains(obj, filter_value)">
            <router-link :to="'/docs/' + documentation_data[obj].n">
            {{ documentation_data[obj].n }}
            </router-link>
            <div v-show="show_descriptions">
              <div class="description_small" v-html="documentation_data[obj].d"/>
            </div>
          </div>
        </div>
      </div>
      <span/>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocObjectList",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    object_categories: {
      type: Object,
      required: true,
    },
  },
  data() {
    return { 
      sorted_categories: Object.keys(this.object_categories).sort(), 
      show_related_objects: {},
      show_descriptions: false,
      filter_value: "",
    };
  },
  created() {
    let keys_array = Object.keys(this.object_categories);
    for(let i = 0; i < keys_array.length; i++) {
      this.show_related_objects[keys_array[i]] = false;
    }
  },
  methods: {
    toggle_all() {
      //Toggle based on the first key
      let keys_array = Object.keys(this.object_categories);
      let first = !(this.show_related_objects[keys_array[0]]);
      for(let i = 0; i < keys_array.length; i++) {
        this.show_related_objects[keys_array[i]] = first;
      }
    },
    filter_all(value) {
      //Toggles on all categories, then filters them
      let keys_array = Object.keys(this.object_categories);
      for(let i = 0; i < keys_array.length; i++) {
        this.show_related_objects[keys_array[i]] = true;
      }
      this.filter_value = value;
    },
    string_contains(string, value) {
      //Blank string means no filter is set
      return (value == "" ? true : string.includes(value));
    },
    any_strings_contain(strings, value) {
      if (value == "") return true;
      for(let i = 0; i < strings.length; i++) {
        if (strings[i].includes(value)) return true;
      }
      return false;
    }
  },
};
</script>

<style scoped>
  .description_small {
    text-align: left;
    white-space: pre-wrap;
    padding-left: 1em;
  }
</style>